 import {Component, EventEmitter, Input, Output, ViewChild, OnInit} from '@angular/core';
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { AddnewModalComponent } from './addnew/addnew-modal.component';
 import {MessageBox} from '../../lib/Alet/message-box';
 import {globalVariables} from '../../shared/globalVariables';
 import {MyTableDataSource} from '../../lib/myInlineEditTable/MyTableDataSource';
 import {ProductTableValidator} from './ProductTableValidator';
 import {ProductsServices} from '../../services/ProductsServices';

/**
 * @title Data table with sorting, pagination, and filtering.
 */

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  displayedColumns = ['id', 'name', 'productLine', 'actionsColumn'];
  dataSource: MyTableDataSource<UserData>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  productsList = [];
  productLines = [];

  constructor(public dialog: MatDialog, public productTableValidator: ProductTableValidator, private productsServices: ProductsServices) {

    this.productsServices.GetProducts().subscribe((res: any) => {
        this.productsList = res["data"];
        this.dataSource = new MyTableDataSource(this.productsList, UserData, this.productTableValidator);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );

    this.productsServices.productLines().subscribe((res: any) => {
        this.productLines = res["data"];

      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );


  }

  ngOnInit() {
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  addNew() {
    let dialogRef = this.dialog.open(AddnewModalComponent, {
      // width: '250px',

      data: {'name': '', productLines: this.productLines}
    });


    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      this.productsServices.createProduct(result.name, result.selectedProductLine.id).subscribe((res: any) => {
          globalVariables.alert("Product Created Successfully.");
          var temp: any = {};
          temp.id = res["data"].id;
          temp.name = result.name;
          temp.lineId = result.selectedProductLine.id;

          this.dataSource.createFromData(temp);
        },
        (err: any) => {

          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
    });


  }

  changeRowDateTextValue(event, row) {
    //row.offVisitReason=  this.offVisitreasons.filter(obj=>obj.offVisitReasonId==row.offVisitReasonId)[0].offVisitreasonName;
  }

  startEdit(row) {
    this.dataSource.startEdit(row)
  }

  cancel(row) {
    this.dataSource.cancel(row)
  }

  delete(row) {

    MessageBox.show("Are you sure delete this row?", "Confirm", "", 2).subscribe(result => {
      if (result["button"] == "yes") {
        this.productsServices.deleteProduct(row.id).subscribe((res: any) => {

            // var dsData = this.dataSource.data;
            const index = this.productsList.findIndex(obj => obj["id"] === row.id);
            this.dataSource.delete(index)


            // this.offVisitsList=  this.offVisitsList.filter(obj=>obj.offVisitId!=row.currentData.offVisitId);
            //this.dataSource.data = this.offVisitsList;
          },
          (err: any) => {
            globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))
          });
      }
    });


  }


  update(row) {
    if (!row.validator.valid) {
      globalVariables.alert("Please enter all required fields.")
      return;
    }


    this.productsServices.updateProduct(row.id, row.name, row.lineId).subscribe((res: any) => {
        this.dataSource.confirmEdit(row)

        //بهجت  هتضاف لو بعمل new inline
        // this.offVisitsList.filter(obj=>obj.offVisitId==row.currentData.offVisitId)[0].offVisitReason= this.offVisitreasons.filter(obj=>obj.offVisitReasonId==row.currentData.offVisitReasonId)[0].offVisitreasonName;
        globalVariables.alert("Data Updated Successfully.")
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });

  }


  export() {
    // specify how you want to handle null values here

    const header = [{"key": "id", "title": "ID"}, {"key": "name", "title": "Name"}, {
      "key": "lineId",
      "title": "Line",
      "referenceList": this.productLines,
      uniqueField: "id",
      fieldName: "name"
    }];


    const items = this.productsList
    const replacer = (key, value) => value === null ? '' : value;

    let csv = items.map(row => header.map(headerItem => {

        if (headerItem["referenceList"]) {
          var x = headerItem["referenceList"].find(item => item[headerItem["uniqueField"]] == row[headerItem["key"]])[headerItem["fieldName"]];

          return JSON.stringify(x, replacer)
        }
        else {
          return JSON.stringify(row[headerItem["key"]], replacer)
        }

      }
    ).join(','));

    let headetTitel = header.map(item => item["title"])

    csv.unshift(headetTitel.join(','))
   var mycsv = csv.join('\r\n')


    var downloadLink = document.createElement("a");
    var blob = new Blob(["\ufeff", mycsv]);
    var url = URL.createObjectURL(blob);
    downloadLink.href = url;
    downloadLink.download = "DataDump.csv";  //Name the file here
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  }


  print() {
    let printContents, popupWin;
    printContents = document.getElementById('print-section').outerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          //........Customized style.......
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    popupWin.document.close();
  }
}


 export class UserData {
   id: string;
   name: string;


 }




