import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from '../../lib/auth/auth.service';
import { MyEvents } from '../../lib/MyEvent';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';
import { ScheduleModalComponent } from './schedule-modal/schedule-modal.component';
import {ScheduleProvidersServcies} from '../../services/schedule-providers-servcies.service';
import {globalVariables} from '../../shared/globalVariables';
import {IProvider} from '../../interfaces/IProvider';
import {DateTimeService} from '../../lib/DateTimeService';
import {MessageBox} from '../../lib/Alet/message-box';


@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.scss']
})

// @ViewChild('gridView') gridView;


export class ScheduleComponent implements OnInit {
  animal: string;
  name: string;
  myAssignedProviders:Array<IProvider>=[];
  columnNum;
  constructor(public dateTimeService:DateTimeService, private scheduleProvidersServcies:ScheduleProvidersServcies, public media: ObservableMedia, public dialog: MatDialog, private myevents :MyEvents, private authService: AuthService) {

    this.scheduleProvidersServcies.MyAssignedProviders(null).subscribe((res: any) => {
    this.myAssignedProviders=res["data"];
  },
  (err: any) => {

    globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

  });


    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 3;
        }
      });
  }

  ngOnInit() {

  }



  openDialogForAdd(Provider:IProvider): void {
    console.log(Provider)
    let dialogRef = this.dialog.open(ScheduleModalComponent, {

      data: { id: Provider.id,selectedDate:null, minDate:new Date() }
    });

    dialogRef.afterClosed().subscribe(result => {
if(!result)
  return;
      this.scheduleProvidersServcies.addSchedule(result.id ,this.dateTimeService.DateToStringDateOnly( result.selectedDate)).subscribe((res: any) => {
        // this.originalMyAssignedProviders.filter(obj=>obj.id ==result.id )[0].scheduleDate=this.dateTimeService.DateToStringDateOnly( result.selectedDate)
          var xx=this.myAssignedProviders.filter(obj=>obj.id ==result.id )[0];
        xx.scheduleDate=this.dateTimeService.DateToStringDateOnly( result.selectedDate);
          xx.scheduleId=res["data"]["scheduleId"]

        },
        (err: any) => {

          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
    });
  }



  openDialogForEdit(provider:IProvider): void {
    console.log(provider)
    let dialogRef = this.dialog.open(ScheduleModalComponent, {

      data: { id: provider.id,selectedDate:null, minDate:new Date() }
    });

    dialogRef.afterClosed().subscribe(result => {
      if(!result)
        return;

      this.scheduleProvidersServcies.updateSchedule(this.myAssignedProviders.filter(obj=>obj.id ==result.id )[0].scheduleId,this.dateTimeService.DateToStringDateOnly( result.selectedDate)).subscribe((res: any) => {

          this.myAssignedProviders.filter(obj=>obj.id ==result.id )[0].scheduleDate=this.dateTimeService.DateToStringDateOnly( result.selectedDate);


        },
        (err: any) => {

          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
    });
  }



  delete(provider:IProvider)
  {
       MessageBox.show("Are you sure delete it?","Confirm","",2).subscribe( result => {
         console.log(result);

         console.log(provider)
         if(result["button"]=="yes") {
           this.scheduleProvidersServcies.deleteSchedule(provider.scheduleId).subscribe((res: any) => {
               var xx = this.myAssignedProviders.filter(obj => obj.id == provider.id)[0]

               xx.scheduleDate = null;
               xx.scheduleId = null
               ;
             },
             (err: any) => {

               globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

             });
         }
       });


  }


  filterVisit(serviceProviderTypeId)
  {

   return this.myAssignedProviders.filter( obj=>obj.serviceProviderTypeId ==serviceProviderTypeId);
  }

}
