import {Injectable} from "@angular/core";
import {globalVariables} from "../shared/globalVariables";

@Injectable()
export class ServerLogs {


    // constructor ( ) {
    //     console.log("init ServerLogs")
    // }


    static setUserIdentifier(id: string) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: setUserIdentifier', id);
            return;
        }

        (<any>window).fabric.Crashlytics.setUserIdentifier(id);
    }

    static setUserName(name: string) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: setUserName', name);
            return;
        }

        (<any>window).fabric.Crashlytics.setUserName(name);
    }

    static setUserEmail(email: string) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: setUserEmail', email);
            return;
        }

        (<any>window).fabric.Crashlytics.setUserEmail(email);
    }

    static addLog(message: string) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: addLog', message);
            return;
        }

        (<any>window).fabric.Crashlytics.addLog(message);
    }

    static sendNonFatalCrash(message: string, stacktrace?: any) {

        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendNonFatalCrash');
            // console.error(stacktrace);

            return;
        }
            (<any>window).fabric.Crashlytics.sendNonFatalCrash(message, stacktrace);

    }

    static sendSignUp(type: string, success: boolean, attributes?: any) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendSignUp', type, success);
            return;
        }

        (<any>window).fabric.Answers.sendSignUp(type, success, attributes);
    }

    static sendLogin(type: string, success: boolean, attributes?: any) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendLogin', type, success);
            return;
        }

        (<any>window).fabric.Answers.sendLogIn(type, success, attributes);
    }

    static sendCustomEvent(name: string, attributes?: any) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendCustomEvent', name);
            return;
        }

        (<any>window).fabric.Answers.sendCustomEvent(name, attributes);
    }

    static sendStartCheckout(totalPrice: number, currency: string, itemCount: number, attributes?: any) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendStartCheckout', totalPrice, currency, itemCount);
            return;
        }

        (<any>window).fabric.Answers.sendStartCheckout(totalPrice, currency, itemCount, attributes);
    }

    static sendContentView(name: string, type: string, id: string, attributes?: any) {
        if (!(<any>window).fabric) {
            console.warn('fabric not available: sendContentView', name, type, id);
            return;
        }

        (<any>window).fabric.Answers.sendContentView(name, type, id, attributes);
    }
}
