  constructor(public media: ObservableMedia,private productsServices:ProductsServices,private assignProductServices:AssignProductServices) {




    this.productsServices.GetProducts().subscribe((res: any) => {
        this.source=res["data"];
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );

    this.assignProductServices.specialties().subscribe((res: any) => {
        this.specialties=res["data"];
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );

    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;

        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;

        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 4;

        }
        else if(change.mqAlias == 'lg'){
          this.columnNum = 5;

        }

      });

  }


