import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSlideToggle, MatPaginator, MatSort} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';

import {ActivatedRoute, Router} from '@angular/router';
 import {globalVariables} from '../../../../shared/globalVariables';
import {UserInfoDto} from '../../../../dtos/userInfoDto';
import {SelectionModel} from '@angular/cdk/collections';
 import {Subject} from 'rxjs';
 import {MyTableDataSource} from '../../../../lib/myInlineEditTable/MyTableDataSource';
import {DateTimeService} from '../../../../lib/DateTimeService';
import {Location} from '@angular/common';
import {NavParams} from '../../../../shared/nav-params';
import {TargetVisitsServices} from '../../../../services/TargetVisitsServices';
import {AddressBar} from '../../../../app-routing.module';
import {VisitDetailsServices} from '../../../../services/VisitDetailServices';
import {Body} from '../../../../lib/RetrofitAngular';
import {GroupByServices} from '../../../../services/GroupByServices';
import {CompleteModalComponent} from '../../../visits/complete-modal/complete-modal.component';
import {VisitInfoComponent} from './visitInfo/visitInfo.component';


export class MyDataStructure {
  id: string;
  name: string;
count:number;

}
@Component({
  selector: 'visit-details',
  templateUrl: './visit-detail.component.html',
  styleUrls: ['./visit-detail.component.scss']
})
export class VisitDetailComponent implements OnInit{
  columnNum;

  checkList=new SelectionModel<any>(true /* multiple */);
  //mydata =new Subject<TodoItemNode[] >();
  mydata=new Subject<any[] >();

  expanded;


  displayedColumns ;
  dataSource: MyTableDataSource<MyDataStructure>;

  tableData=[];
   employees=[];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  selectedRadio=null;
  currentPage;
  selectedDatePickerTo;
  selectedDatePickerFrom
  type;
  employeeId;
  employeeName;

    productId;
    productName;

     FLMId;
     FLMName;

     districtId;
    districtName;

    specialtyId;
    specialtyName;



  constructor(private router:Router,private navParams:NavParams,private targetVisitsServices:TargetVisitsServices,private _location: Location,private route: ActivatedRoute, private dateTimeService:DateTimeService,private groupByServices:GroupByServices, public media: ObservableMedia, public dialog: MatDialog ) {

    this.type=this.navParams.get("type");

    this.selectedDatePickerFrom= this.dateTimeService.stringToDataTime( this.navParams.get("dateFrom"))
    this.selectedDatePickerTo= this.dateTimeService.stringToDataTime( this.navParams.get("dateTo"))

    if(this.type=="employee")
    {
        this.displayedColumns = ['visitId','firstLineManager','dateTime', 'accountName', 'specialty','brick' ];

        this.employeeId=this.navParams.get("employeeId")
      this.employeeName=this.navParams.get("employeeName")
      this.search()

    }
     else if(this.type=="product")
      {
          this.displayedColumns = ['visitId','employeeName','firstLineManager','dateTime', 'accountName', 'specialty','brick' ];

          this.productId=this.navParams.get("productId")
          this.productName=this.navParams.get("productName")
          this.search()

      }

    else if(this.type=="FLM")
    {
        this.displayedColumns = ['visitId','employeeName','firstLineManager','dateTime', 'accountName', 'specialty','brick' ];

        this.FLMId=this.navParams.get("FLMId")
        this.FLMName=this.navParams.get("FLMName")
        this.search()

    }

    else if(this.type=="district")
    {
        this.displayedColumns = ['visitId','employeeName','firstLineManager','dateTime', 'accountName', 'specialty','brick' ];

        this.districtId=this.navParams.get("districtId")
        this.districtName=this.navParams.get("districtName")

        this.search()

    }

    else if(this.type=="specialty")
    {
        this.displayedColumns = ['visitId','employeeName','firstLineManager','dateTime', 'accountName', 'specialty','brick' ];


        this.specialtyId=this.navParams.get("specialtyId")
        this.specialtyName=this.navParams.get("specialtyName")

        this.search()

    }





      this.navParams.data={};







    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 3;
        }
      });


  }




  goBack()
  {
    this._location.back();
  }

  ngOnInit() {

  }

  searchButton()
  {
    if(!this.selectedDatePickerFrom)
    {
      globalVariables.alert("Please select From Data.")
      return;
    }
    if(!this.selectedDatePickerTo)
    {
      globalVariables.alert("Please select To Data.")
      return;
    }

    this.search( )
  }

  search(  ) {

    this.expanded = false;


    if (this.type == "employee")
    {
      this.groupByServices.visitsList(this.employeeId, this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo) ).subscribe((res: any) => {
          this.tableData = res["data"];
          this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        }
      );
  }

  else if(this.type == "product")
    {
        this.groupByServices.visitsPerProductDetails(this.productId, this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo) ).subscribe((res: any) => {
                this.tableData = res["data"];
                this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
            },
            (err: any) => {
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

            }
        );
    }


    else if(this.type=="FLM")
    {
        this.groupByServices.visitsPerFLMDetails(this.FLMId, this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo) ).subscribe((res: any) => {
                this.tableData = res["data"];
                this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
            },
            (err: any) => {
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

            }
        );
    }

    else if(this.type=="district")
    {
        this.groupByServices.visitsPerDistrictDetails(this.districtId, this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo) ).subscribe((res: any) => {
                this.tableData = res["data"];
                this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
            },
            (err: any) => {
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

            }
        );
    }


    else if(this.type=="specialty")
    {


        this.groupByServices.visitsPerSpecialtyDetails(this.specialtyId, this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo) ).subscribe((res: any) => {
                this.tableData = res["data"];
                this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
            },
            (err: any) => {
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

            }
        );

    }


  }



  goTo(row)
  {


    let dialogRef = this.dialog.open(VisitInfoComponent, {
      width: '480px',
      height: 'auto',
      data: {'visitId':row.visitId}
    });
    dialogRef.afterClosed().subscribe(data => {

      if(data)
      {
        // this.visits=this.visits.filter(obj=>obj.visitId!=data.visit.visitId)
      }
    });
  }



  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }



}


