import {Component, OnInit, Inject, ViewChild, ElementRef} from '@angular/core';
 import {FormBuilder, FormControl, Validators} from '@angular/forms';
import {globalVariables} from '../../../shared/globalVariables';
import {VisitesServices} from '../../../services/VisitesServices';
import {AssignedProvidersServcies} from '../../../services/assigned-providers-servcies.service';
import {ProductsServices} from '../../../services/ProductsServices';
import {DateTimeService} from '../../../lib/DateTimeService';
import {map, startWith} from 'rxjs/internal/operators';
import {Observable, of, Subject} from 'rxjs';
import { Location } from '@angular/common';
import {NavParams} from '../../../shared/nav-params';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';
@Component({
  selector: 'complete-modal',
  templateUrl: './add-hoc-visit.component.html',
  styleUrls: ['./add-hoc-visit.component.scss']
})
export class AddHocVisitComponent implements OnInit {
  columnNum;
  columnNum1;
  formGroup1;
  singleOrDoubleVisit=0;
  selectedManagers=[];
  selectedProducts=[];
  products;
  comment;
  selectedDate;
  minDate=new Date();
  providers;

  districts;
  selectedDistrict;
  specialties;
  selectedSpecialty
 // filterdProviders :Observable<any[]>;
  filterdProviders =[];
  selectedProvider;
   myControl = new FormControl();
  managersOfEmployee;
  public configObservable = new Subject<number>();

  constructor(public media: ObservableMedia,private navParams:NavParams,private _location: Location, public dateTimeService:DateTimeService,private productsServices:ProductsServices,private assignedProvidersServcies:AssignedProvidersServcies,private visitesServices:VisitesServices,private fb: FormBuilder) {
    this.managersOfEmployee= this.navParams.get('managersOfEmployee')
    this.providers= this.navParams.get('providers')
    this.districts= this.navParams.get('districts')
    this.specialties= this.navParams.get('specialties')
    this.filterdProviders=this.providers;
    if(!this.providers)
    {
      this._location.back();
      return;
    }

      this.navParams.data={};

    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
          this.columnNum1 = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
          this.columnNum1 =1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
          this.columnNum1 = 2;
        }
        else if(change.mqAlias == 'lg'){
          this.columnNum = 3;
          this.columnNum1 = 4;
        }

      });
  }




  ngOnInit() {
    console.log("sss")
    // this.filterdProviders=  this.configObservable.pipe(map(val => {
    //   console.log("sss")
    //   return this.filter()
    // }))

     // this.filterdProviders=  this.myControl.valueChanges
     //      .pipe(
     //        startWith(''),
     //        map(value => typeof value === 'string' ? value : (value as any).serviceProviderName),
     //        map(val => {
     //           return this.filter()
     //        })
     //      );


  }


  productBySpecialty(specialtyId)
  {
    this.productsServices.productBySpecialty(specialtyId).subscribe((res: any) => {
        this.products=res["data"]
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );
  }

  onNoClick(): void {
    //this.dialogRef.close();
  }

  onNgModelChange(event){
    console.log('on ng model change', event);
  }


  latestChangeEvent(event)
  {
    this.productBySpecialty(event.value.specialtyId)
    //this.productBySpecialty(event.option.value.specialtyId)
   this.selectedProvider=event.value;

  }


    updateDateToString(event)
    {
        let newDate = new Date(event)
        this.selectedDate=newDate
    }


    displayFn(item): string | undefined {

        return item ? item.serviceProviderName.toString(): undefined;
    }




    specialtyChanged(event)
  {
    this.selectedSpecialty=event.value;
    //this.configObservable.next(1);
   this.filter()
  }

  districtChanged(event)
  {
    this.selectedDistrict=event.value;
    this.filter()

   // this.configObservable.next(2);
  }

  filter()   {
var xx=[];


    if(!this.selectedProvider)// if(!this.myControl.value
    {
      xx= this.providers;
    }
    else {
        xx = this.providers.filter(item => {
        if (item.serviceProviderName) {
          return item.serviceProviderName.toLowerCase().indexOf(this.selectedProvider.toLowerCase()) === 0
        }
        else {
          return false;
        }
      });
    }

    if(this.selectedDistrict)
    {
      xx=xx.filter(item=> item.districtId == this.selectedDistrict.id)
      //this.myControl.setValue("")

      this.selectedProvider=null;
    }

    if(this.selectedSpecialty)
    {
      xx=xx.filter(item=> item.specialtyId == this.selectedSpecialty.id)
      //this.myControl.setValue("")
      this.selectedProvider=null;
    }



   // this.filterdProviders=of(xx)
//return  xx;

    this.filterdProviders=xx;
  }





  complete()
  {

    if(!this.selectedDate)
    {
      globalVariables.alert("Please select Date")
      return;
    }

    if(this.selectedProducts.length==0)
    {
      globalVariables.alert("Please select one Product at least.")
      return;
    }



    if( this.singleOrDoubleVisit==1 &&this.selectedManagers.length==0)
    {
      globalVariables.alert("Please select one manager at least.")
      return;
    }


    if(!this.comment)
    {
      globalVariables.alert("Please inter a comment.")
      return;
    }


    var selectedManagersIds = this.selectedManagers.map(manager => manager.id);
    var selectedProductsIds = this.selectedProducts.map(product => product.id);


    this.visitesServices.adhocVisit(this.selectedProvider.id,this.dateTimeService.DateToStringDateOnly(this.selectedDate), selectedProductsIds, this.singleOrDoubleVisit, selectedManagersIds, this.comment).subscribe((res: any) => {

        globalVariables.alert("Visit completed successfully ")


        this.selectedDate=null;
        this.selectedDistrict=null;
        this.selectedManagers=[];
        this.selectedProducts=[];
        this.products=null;
        //this.myControl.setValue("")

        this.selectedProvider=null;
        this.selectedSpecialty=null;
        this.comment='';



      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );




  }
  goBack() {


    this._location.back();
  }

}


