import {Component, OnInit, Pipe, PipeTransform, ViewChild} from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSlideToggle, MatPaginator, MatSort} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';

import {ActivatedRoute, Router} from '@angular/router';
import {CoachingServices} from '../../../services/CoachingServices';
import {globalVariables} from '../../../shared/globalVariables';
import {UserInfoDto} from '../../../dtos/userInfoDto';
import {SelectionModel} from '@angular/cdk/collections';
import {forkJoin, Observable, Subject} from 'rxjs';
import {MyTableDataSource} from '../../../lib/myInlineEditTable/MyTableDataSource';
import {DateTimeService} from '../../../lib/DateTimeService';
import {AddressBar} from '../../../app-routing.module';
import {NavParams} from '../../../shared/nav-params';
import {LocationStrategy} from '@angular/common';
import {VisitesServices} from "../../../services/VisitesServices";
import {FilterPipe} from "./FilterPipe";


export class MyDataStructure {
    id: string;
    name: string;
    count:number;

}
@Component({
    selector: 'visit-count-report',
    templateUrl: './visit.count.report.component.html',
    styleUrls: ['./visit.count.report.component.scss']
})
export class VisitCountReportComponent implements OnInit {
    columnNum;
    expanded;
    selectedDatePickerFrom;
    selectedDatePickerTo;
    emp=[];
    checkList=new SelectionModel<any>(true /* multiple */);
    //mydata =new Subject<TodoItemNode[] >();
    mydata=new Subject<any[] >();

    displayedColumns = [  'productline', 'regional','flm','mp','count' ];
    dataSource: MyTableDataSource<MyDataStructure>;
    tableData=[];
    visitCountEmployees=[];
    visitCountProducts=[];
    visitCountProductLines=[];
    visitCountSpecialtys=[];

    selectedRegionalManager=null;
    selectedFLM=null;
    selectedEmployee=null;
    RegionalManagerFilter={type:"RegionalManager"}
    FLMFilter;
    employeeFilter

    selectedProductLine=null;
    selectedProduct=null;
    selectedSpeciality=null;
     productFilter;
    specialityFilter;

    AMOrPM=[{name:"AM",id:1},{name:"PM",id:2}]
    selectedAMOrPM=null;

    private paginator: MatPaginator;
    private sort: MatSort;
    @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
        this.paginator = mp;
        if(this.dataSource) {
            this.dataSource.paginator = this.paginator;
        }
    }

    @ViewChild(MatSort) set matSort(mp: MatSort) {
        this.sort = mp;
        if(this.dataSource)
        {
            this.dataSource.sort = this.sort;
        }
    }

    constructor(  private navParams:NavParams,public route: ActivatedRoute, private router: Router, private dateTimeService:DateTimeService,private visitesServices:VisitesServices,  public media: ObservableMedia, public dialog: MatDialog ) {

        var date = new Date();
        this.selectedDatePickerFrom = new Date(date.getFullYear(), date.getMonth(), 1);
        this.selectedDatePickerTo = date;
        // return forkJoin(this.http.get('jsonplaceholder.typicode.com/posts'), this.http.get('jsonplaceholder.typicode.com/users'));
globalVariables.showProgressDialog()
        forkJoin(
            this.visitesServices.visitCountEmployeeFilter(),
            this.visitesServices.visitCountProductFilter(),
            this.visitesServices.visitCountProductLineFilter(),
            this.visitesServices.visitCountSpecialtyFilter(),
        )
            .subscribe(res => {
                    this.visitCountEmployees=res[0].data;
                    this.visitCountProducts=res[1].data;
                    this.visitCountProductLines=res[2].data;
                    this.visitCountSpecialtys=res[3].data;
                    globalVariables.dismissProgressDialog();
                     this.employeeFilter={type:"Employee"}
                      this.FLMFilter={type:"FLM"}
                },
                (err: any) => {
                    globalVariables.dismissProgressDialog();
                    globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))
                });


        media.asObservable()
            .subscribe((change: MediaChange) => {
                // alert(change.mqAlias);
                console.log(change.mqAlias);
                if(change.mqAlias == 'xs'){
                    this.columnNum = 1;
                }
                else if(change.mqAlias == 'sm'){
                    this.columnNum = 1;
                }
                else if(change.mqAlias == 'md'){
                    this.columnNum = 2;
                }
                else{
                    this.columnNum = 3;
                }
            });
    }




    search()
    {

        if(!this.selectedDatePickerFrom)
        {
            globalVariables.alert("Please select From Data.")
            return;
        }
        if(!this.selectedDatePickerTo)
        {
            globalVariables.alert("Please select To Data.")
            return;
        }




        this.expanded=false;
        setTimeout(()=>{
var temp={
    "regional":this.selectedRegionalManager,
    "flm": this.selectedFLM,
    "mp":this.selectedEmployee,
    "productline":this.selectedProductLine,
    "specialty": this.selectedSpeciality,
    "product": this.selectedProduct,
    "datefrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),
    "dateto":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo),
    "ampm":this.selectedAMOrPM
}

            this.visitesServices.visitCountReport(temp).subscribe((res: any) => {
                this.expanded=false;
                this.tableData = res["data"];
                this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
              },
              (err: any) => {
                this.expanded=false;
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

              }
            );

        }, 100)


    }


    ngOnInit() {
    }


    selectedRegionalManagerChanged(event)
    {
        this.selectedRegionalManager=event.value
        this.FLMFilter={type:"FLM",managerId:this.selectedRegionalManager}
    }
    selectedFLMChanged(event)
    {
        this.selectedFLM=event.value
        this.employeeFilter={type:"Employee",managerId:this.selectedFLM}
    }
    selectedEmployeeChanged(event)
    {
        this.selectedEmployee=event.value
    }

    selectedProductLineChanged(event)
    {
        this.selectedProductLine=event.value;
        this.productFilter={type:"productFilter","selectedProductLine":this.selectedProductLine}

    }

    selectedProductChanged(event)
    {
        this.selectedProduct=event.value;
        this.specialityFilter={type:"specialityFilter","selectedProduct":this.selectedProduct}

    }

    selectedSpecialityChanged(event)
    {
        this.selectedSpeciality=event.value;
    }

    resetRegionalManager()
    {
        this.selectedRegionalManager=null;
        this.selectedFLM=null;
        this.selectedEmployee=null;
    }
    resetFLM()
    {
        this.selectedFLM=null;
        this.selectedEmployee=null;
    }

    resetEmployee()
    {
        this.selectedEmployee=null;
    }


    resetProductLine()
    {
        this.selectedProductLine=null;
        this.selectedProduct=null;
        this.selectedSpeciality=null;
    }

    resetProduct()
    {
        this.selectedProduct=null;
        this.selectedSpeciality=null;
    }
    resetSpeciality()
    {
        this.selectedSpeciality=null;
    }

    goTo(row)
    {
        // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] ,{ queryParams: { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)} }  );
        // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}]    );
        // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}] , { skipLocationChange: true}  );

        if(row.count<=0)
        {
            globalVariables.alert("Employee doesn't have any visit.")
            return;
        }
        else {
            // this.router.navigateByData({
            //    url: [AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] ,
            //    data:  { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}, //data - <any> type
            //    //extras: {} - <NavigationExtras> type, optional parameter
            //  });

            this.navParams.data =  {
                "dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),
                "dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo),
                "regional": this.selectedRegionalManager ,
                "flm":this.selectedFLM,
                "mp": row.mpid,
                "productline":this.selectedProductLine,
                "product":this.selectedProduct,
                "ampm":this.selectedAMOrPM


            }

            this.router.navigate([AddressBar.VisitCountReport+'/'+ AddressBar.VisitCountDetailsReport] );


        }

    }


    export() {
        // specify how you want to handle null values here
        // {
        //     "productlineid": 1,
        //     "productline": "BU1",
        //     "regional": "CRSM",
        //     "regionalid": 106,
        //     "flm": "UPPEREGYPTBU1",
        //     "flmid": 109,
        //     "mp": "TP32BU1",
        //     "mpid": 142,
        //     "count": 533
        // }

        const header = [
            {"key": "productline", "title": "Product Line"},
            {"key": "regional", "title": "Regional"},
            {"key": "flm", "title": "FLM"},
            {"key": "mp", "title": "MR"},
            {"key": "count", "title": "Count"},
          ];


        const items = this.tableData
        const replacer = (key, value) => value === null ? '' : value;

        let csv = items.map(row => header.map(headerItem => {

                // if (headerItem["referenceList"]) {
                //     var x = headerItem["referenceList"].find(item => item[headerItem["uniqueField"]] == row[headerItem["key"]])[headerItem["fieldName"]];
                //
                //     return JSON.stringify(x, replacer)
                // }
                // else {
                    return JSON.stringify(row[headerItem["key"]], replacer)
                // }

            }
        ).join(','));

        let headetTitel = header.map(item => item["title"])

        csv.unshift(headetTitel.join(','))
        var mycsv = csv.join('\r\n')


        var downloadLink = document.createElement("a");
        var blob = new Blob(["\ufeff", mycsv]);
        var url = URL.createObjectURL(blob);
        downloadLink.href = url;
        downloadLink.download = "DataDump.csv";  //Name the file here
        document.body.appendChild(downloadLink);
        downloadLink.click();
        document.body.removeChild(downloadLink);
    }

}




