import { MatDialog, MAT_DIALOG_DATA } from "@angular/material";
import { SimpleDialogComponent } from "./simple-dialog.component";

export class MessageBox {
  public static   dialog: MatDialog;
  public static show(  message, title = "Alert", information = "", button = 0, allow_outside_click = false,showInput=false,inputText="", style = 0, width?  ) {

    const dialogRef = MessageBox.dialog.open( SimpleDialogComponent, {
      data: {
        title: title || "Alert",
        message: message,
        information: information,
        button: button || 0,
        allow_outside_click: allow_outside_click || false,
        showInput:showInput,
        inputText:inputText,
        style: style || 1


      },
      width: width
    });
    return dialogRef.afterClosed();
  }
}

export  enum MessageBoxButton {
    Ok = 0,
    OkCancel = 1,
    YesNo = 2,
    AcceptReject = 3
}

export  enum MessageBoxStyle {
    Simple = 0,
    Full = 1
};
