@Directive({
  selector: '[myHideIfNotHaseRole]'
})
export class MyHideIfNotHaseRoleDirective implements OnInit {
  @Input('myHideIfNotHaseRole') role_s; // Required permission passed in
  constructor(private el: ElementRef, private authorizationService: AuthService) { }
  ngOnInit() {
    if (!this.authorizationService.checkIfUserHasRole(this.role_s)) {
      this.el.nativeElement.style.display = 'none';
    }
  }
}


