import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { OffVisitComponent } from '../off-visit.component';
import {FormBuilder, Validators} from '@angular/forms';

@Component({
  selector: 'app-off-modal',
  templateUrl: './off-modal.component.html',
  styleUrls: ['./off-modal.component.scss']
})
export class OffModalComponent implements OnInit {

  formGroup1;

  constructor( private fb: FormBuilder, public dialogRef: MatDialogRef<OffModalComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
    this.formGroup1 = fb.group({
      dropSelect: [this.data.selectedReason, [Validators.required]], //, Validators.email
      date: ['', Validators.required]
    });



  }

  ngOnInit() {
  }
  onNoClick(): void {
    this.dialogRef.close();
  }


}
