import {Component, EventEmitter, Input, Output, ViewChild, ViewEncapsulation} from '@angular/core';
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { OffModalComponent } from './off-modal/off-modal.component';
import {OffVisitServices} from '../../services/OffVisitServices';
import {globalVariables} from '../../shared/globalVariables';
import {IOffVisit, OffVisit} from '../../interfaces/IOffVisit';

import {OffVisitValidator} from './OffVisitValidator';
import {DateTimeService} from '../../lib/DateTimeService';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {MessageBox} from '../../lib/Alet/message-box';


@Component({
  selector: 'app-off-visit',
  templateUrl: './off-visit.component.html',
  styleUrls: ['./off-visit.component.scss'],
    encapsulation: ViewEncapsulation.None
})


export class OffVisitComponent{

  offVisitreasons;

   offVisitsList :Array<OffVisit>=[];


  constructor(public dateTimeService:DateTimeService,public offVisitValidator:OffVisitValidator,public dialog: MatDialog,private offVisitServices:OffVisitServices) {

  }



  addNew() {
    let dialogRef = this.dialog.open(OffModalComponent, {
      // width: '250px',
      data: {offVisitreasons:this.offVisitreasons}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
var date=this.dateTimeService.DateToStringDateOnly(result.selectedDate)
      this.offVisitServices.createOffVisit(result.selectedReason,date,result.comment).subscribe((res: any) => {
          globalVariables.alert("Off Visit Created Successfully.")
          //this.dataSource.createNew();
          var temp:any={};
          temp.offVisitId=res["data"].offVisitId
          temp.offVisitReasonId=result.selectedReason;
          temp.offVisitReason=this.offVisitreasons.filter(obj=>obj.offVisitReasonId==result.selectedReason)[0].offVisitreasonName;
          temp.comment=result.comment
          temp.status="Pending Approval";
          temp.statusId=1
          temp.offVisitDate=date;
          temp.employeeName=UserInfoDto.fullName;

          this.offVisitsList.unshift(temp);
        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
      });
  }



  updateFromPopup(row:IOffVisit) {
    let dialogRef = this.dialog.open(OffModalComponent, {
      // width: '250px',
      data: {offVisitreasons:this.offVisitreasons,  comment:row.comment,selectedReason:row.offVisitReasonId,selectedDate:this.dateTimeService.stringToDataTime(row.offVisitDate)}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      var date=this.dateTimeService.DateToStringDateOnly(result.selectedDate)

      this.offVisitServices.updateOffVisit(row.offVisitId,result.selectedReason,date,result.comment).subscribe((res: any) => {
          globalVariables.alert("Off Visit Created Successfully.")


          for (var i = 0; i < this.offVisitsList.length; i++) {
            if( this.offVisitsList[i].offVisitId==row.offVisitId)
            {
              this.offVisitsList[i].offVisitReasonId=result.selectedReason
             var temp= this.offVisitreasons.filter(obj=>obj.offVisitReasonId==result.selectedReason)[0].offVisitreasonName
              this.offVisitsList[i].offVisitReason=temp;
              this.offVisitsList[i].comment=result.comment
              this.offVisitsList[i].status=row.status;
              this.offVisitsList[i].statusId=row.statusId
              this.offVisitsList[i].offVisitDate=date;
              this.offVisitsList[i].employeeName=UserInfoDto.fullName;
            }
          }

        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
    });
  }

  ngAfterViewInit() {
    this.offVisitServices.offVisitReason().subscribe((res: any) => {
        this.offVisitreasons=res["data"]
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });

    this.offVisitServices.offVisits().subscribe((res: any) => {
        this.offVisitsList=res["data"];
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });
  }




  delete(row)
  {
    MessageBox.show("Are you sure delete row?","Confirm","",2).subscribe( result => {
      if(result["button"]=="yes") {
        this.offVisitServices.deleteOffVisit(row.offVisitId).subscribe((res: any) => {
            this.offVisitsList =  this.offVisitsList.filter(obj=>obj.offVisitId!=row.offVisitId);
          },
          (err: any) => {
            globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))
          });
      }
    });
  }


  update(row)
  {
    if (!row.validator.valid)
    {
      globalVariables.alert("Please enter all required fields.")
      return;
    }
    var date;
    if(typeof   row.currentData.offVisitDate =="string")
    {
        date=row.currentData.offVisitDate
    }
    else
    {
        date=this.dateTimeService.DateToStringDateOnly(row.currentData.offVisitDate)
    }

    this.offVisitServices.updateOffVisit(row.currentData.offVisitId,row.currentData.offVisitReasonId,date,row.currentData.comment).subscribe((res: any) => {
        row.confirmEditCreate();
        globalVariables.alert("Off Visit Updated Successfully.")
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });

  }



}


