import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../lib/auth/auth.service';
import { MyEvents } from '../../lib/MyEvent';
import { MatDialog } from '@angular/material';
import { ObservableMedia, MediaChange } from '@angular/flex-layout';
import {VisitesServices} from '../../services/VisitesServices';
import {DateTimeService} from '../../lib/DateTimeService';
import {globalVariables} from '../../shared/globalVariables';
import {MessageBox} from '../../lib/Alet/message-box';
import { OffModalComponent } from '../off-visit/off-modal/off-modal.component';
import { CompleteModalComponent } from './complete-modal/complete-modal.component';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {map, startWith} from 'rxjs/internal/operators';
import {AssignedProvidersServcies} from '../../services/assigned-providers-servcies.service';
import {NgxSpinnerService} from '../../lib/spinner/ngx-spinner.service';
import {AddressBar} from '../../app-routing.module';
import {ActivatedRoute, Router} from '@angular/router';
import {NavParams} from '../../shared/nav-params';

@Component({
  selector: 'app-visits',
  templateUrl: './visits.component.html',
  styleUrls: ['./visits.component.scss']
})
export class VisitsComponent implements OnInit {

  columnNum;
  columnNum1;
  visits=[];
  isAllSelected=false;
  isIndeterminate=false;
  selectedDatePickerFrom=new Date();
  selectedDatePickerTo=new Date();
  managersOfEmployee=[];
  providers;
  showSpinner = true;
  errorMessage = "";
  threeServices=5;
  districts;
  specialties;
  constructor(public route: ActivatedRoute, private navParams:NavParams,private router: Router, public assignedProvidersServcies:AssignedProvidersServcies,public dateTimeService:DateTimeService,public visitesServices:VisitesServices,public media: ObservableMedia, public dialog: MatDialog, private myevents :MyEvents,private authService: AuthService,) {

    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
          this.columnNum1 = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
          this.columnNum1 =1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
          this.columnNum1 = 2;
        }
        else if(change.mqAlias == 'lg'){
          this.columnNum = 3;
          this.columnNum1 = 4;
        }

      });




  }



  ngOnInit() {

    this.retry();

  }

  retry()
  {
    this.assignedProvidersServcies.assignedServiceProvider().subscribe((res: any) => {
        this.providers=res["data"]
        this.threeServices--;
        if(this.threeServices==0) {
          this.showSpinner = false;
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this.errorMessage=this.errorMessage + "   " +globalVariables.getErrorMessage(err);      }
    );



    this.assignedProvidersServcies.assignedServiceProviderDistrict().subscribe((res: any) => {
        this.districts=res["data"]
        this.threeServices--;
        if(this.threeServices==0) {
          this.showSpinner = false;
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this.errorMessage=this.errorMessage + "   " +globalVariables.getErrorMessage(err);      }
    );

    this.assignedProvidersServcies.assignedServiceProviderSpecialty().subscribe((res: any) => {
        this.specialties=res["data"]
        this.threeServices--;
        if(this.threeServices==0) {
          this.showSpinner = false;
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this.errorMessage=this.errorMessage + "   " +globalVariables.getErrorMessage(err);      }
    );


    this.search(this.dateTimeService.DateToStringDateOnly( new Date()),this.dateTimeService .DateToStringDateOnly( new Date()))


    this.visitesServices.managersOfEmployee().subscribe((res: any) => {
        this.managersOfEmployee=res["data"]
        this.threeServices--;
        if(this.threeServices==0) {
          this.showSpinner = false;
        }

      },
      (err: any) => {
        this.showSpinner = false;
        this.errorMessage=this.errorMessage + "   " +globalVariables.getErrorMessage(err);      }
    );
  }






  search(from,to)
  {
    if(!from)
    {
      if(this.selectedDatePickerFrom) {
        from = this.dateTimeService .DateToStringDateOnly(this.selectedDatePickerFrom);
      }
      else
      {
        from=this.dateTimeService .DateToStringDateOnly( new Date())
      }
    }
    if(!to)
    {
      if(this.selectedDatePickerTo) {
        to = this.dateTimeService .DateToStringDateOnly(this.selectedDatePickerTo);
      }
      else
      {
        to=this.dateTimeService .DateToStringDateOnly( new Date())
      }
    }
    this.visitesServices.visits(from,to,null).subscribe((res: any) => {
        this.visits=res["data"]
        this.threeServices--;
        if(this.threeServices==0) {
          this.showSpinner = false;
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this.errorMessage=this.errorMessage + "   " +globalVariables.getErrorMessage(err);
       // globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );
  }

  filterVisit(serviceProviderTypeId)
  {
    return this.visits.filter( obj=>obj.serviceProviderTypeId ==serviceProviderTypeId);
  }


  changeBox(event,item)
  {
    if(event.checked===true) {
      item.selected=true
       if(this.visits.filter(obj=>!obj["selected"]).length>0)
       {
         this.isIndeterminate=true;
       }
       else
       {
         this.isAllSelected=true;
         this.isIndeterminate=false;
       }

    }
    else
    {
      item.selected=false
      if(this.visits.filter(obj=>!obj["selected"]).length==this.visits.length)
      {
        this.isAllSelected=false;
        this.isIndeterminate=false;

      }
      else
      {

        this.isIndeterminate=true;
      }

    }


  }


selectUnSelectAll()
{

  this.isAllSelected=! this.isAllSelected;
  this.visits.map(obj=>
  {
    obj["selected"]=this.isAllSelected;
  });

  this.isIndeterminate=false
}




comment(visit)
{
  MessageBox.show("","Comment","",1,true,true,visit.comment).subscribe( result => {
   console.log(result);
   if(result["button"]=="ok"&&result["inputText"])
   {

     this.visitesServices.comment(visit.visitId,result["inputText"]).subscribe((res: any) => {
         // globalVariables.alert("comment added successfully ")
         this.visits.filter(obj=>obj.visitId==visit.visitId)[0].comment=result["inputText"];


       },
       (err: any) => {
         globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

       }
     );
   }
 });


}


  openPoup(visit)
  {
    let dialogRef = this.dialog.open(CompleteModalComponent, {
      // width: '250px',
      height: 'auto',
      data: {'managersOfEmployee':this.managersOfEmployee,visit:visit }
    });
    dialogRef.afterClosed().subscribe(data => {

     if(data)
     {
       this.visits=this.visits.filter(obj=>obj.visitId!=data.visit.visitId)
     }
    });


  }






  adhoc()
  {

    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] ,{ queryParams: { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)} }  );
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}]    );
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}] , { skipLocationChange: true}  );
    //
    //
    // this.navParams.data =  { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] );
    //
    this.navParams.data =  {'managersOfEmployee':this.managersOfEmployee ,"providers":this.providers ,"districts":this.districts,"specialties":this.specialties}
    this.router.navigate([AddressBar.VisitsComponent+'/'+AddressBar.AddHocVisitComponent])

  }
}


export class CheckboxConfigurableExample {
  checked = false;
  indeterminate = false;
  labelPosition = 'after';
  disabled = false;
}
