import { Component, OnInit, Inject } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA, MatDatepickerInputEvent} from '@angular/material';
 import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidatorFn, Validators} from '@angular/forms';
import {DateTimeService} from "../../../lib/DateTimeService";
import {globalVariables} from "../../../shared/globalVariables";
import {AddressBar} from "../../../app-routing.module";
import {Router} from "@angular/router";
import {PlanServices} from "../../../services/PlanServices";
import {MyTableDataSource} from "../../../lib/myInlineEditTable/MyTableDataSource";
import {MyDataStructure} from "../../reports/groupBy/groupBy.component";
import {NavParams} from "../../../shared/nav-params";
import {MyEvents} from "../../../lib/MyEvent";

@Component({
  selector: 'create-new-plan-modal',
  templateUrl: './create-new-modal.html',
  styleUrls: ['./create-new-modal.scss']
})
export class CreateNewModal implements OnInit {

  formGroup1;

    years=[];
    months=[];
    selectedYear;
    minDate;
    maxDate;
    selectedMonth;
    startAt;
    subject;
    startDate;
    endDate;
  constructor(private myEvents:MyEvents,private navParams:NavParams,private planServices:PlanServices,private router: Router,public dateTimeService:DateTimeService,private fb: FormBuilder, public dialogRef: MatDialogRef<CreateNewModal>, @Inject(MAT_DIALOG_DATA) public data: any) {

       for(var i = new Date().getFullYear(); i < new Date().getFullYear()+5; i++){
           this.years.push({"id":i});
       }




      this.formGroup1 = this.fb.group({

          'dropSelectYear': new FormControl(this.selectedYear, [
              Validators.required,
           ]),

          'dropSelectMonth': new FormControl(this.selectedMonth, [
              Validators.required,
          ]),

          // 'startDate': new FormControl(this.startDate, [
          //     Validators.required,
          // ]),
          //
          // 'endDate': new FormControl(this.endDate, [
          //     Validators.required,
          // ]),

      });


  }


    yearChangede(event)
    {
        this.months=[];
        var temp=[{"id":'January',"index":0}, {"id":'February',"index":1}, {"id":'March',"index":2}, {"id": 'April',"index":3}, {"id": 'May',"index":4}, {"id": 'June',"index":5}, {"id": 'July',"index":6}, {"id": 'August',"index":7}, {"id": 'September',"index":8}, {"id": 'October',"index":9}, {"id": 'November',"index":10}, {"id": 'December',"index":11}]
if(event.value==new Date().getFullYear()) {

    for (var i = new Date().getMonth(); i < 12; i++) {
        this.months.push(temp[i]);
    }
}
else
{
    this.months=temp;
}

        this.setMaxMinDate(null);

    }

    submit()
    {

    }


    startPlan()
    {
        var startDate =  new Date(this.selectedYear,this.selectedMonth)
           var endDate =new Date(this.selectedYear,this.selectedMonth+1,0);

        this.planServices.createPlan(this.dateTimeService.DateToStringDateOnly(startDate),this.dateTimeService.DateToStringDateOnly(endDate),this.subject).subscribe((res: any) => {

                this.navParams.data={};
                this.navParams.data["endDate"] =res["data"].endDate;
                this.navParams.data["startDate"]=res["data"].startDate;
                this.navParams.data["planId"]=res["data"].planId;

                this.navParams.data["status"]="DRAFT";
                this.navParams.data["subject"]=this.subject;
                this.navParams.data["comment"]="";
                this.myEvents.publish("createPlanlist", this.navParams.data);

                 this.router.navigate([AddressBar.MyPlan+'/'+ AddressBar.PlanScheduler] );
                this.dialogRef.close();

            },
            (err: any) => {
                globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

            }
        );
    }


    checkDates()
    {
        if(this.startDate && this.endDate)
        {
            if(this.endDate<this.startDate)
            {
                // globalVariables.alert("startDate can't be greater than endDate")
                // return {"startDate can't be greater than endDate": true };//error
                //
                return true;

            }
            else
            {
                return false;
            }
        }
        else
        {
            return false;
        }

    }

    dateLessThan(from: string, to: string) {
        return (group: FormGroup): {[key: string]: any} => {
            let f = group.controls[from];
            let t = group.controls[to];
            if (f.value > t.value) {
                   return {"Start Date should be less than End Date.": true};
            }
            return {};
        }
    }

    customValidatorsStartDate(c)
  {
    if(!this.endDate)
      return null;

    if(c.value>this.endDate)
    {
      globalVariables.alert("startDate can't be greater than endDate")
        return {"startDate can't be greater than endDate": true };//error

    }
    else
    {
      return null;
    }


  }

    customValidatorsEndDate(c)
    {



    }

  ngOnInit() {
  }
  onNoClick(): void {
    this.dialogRef.close();
  }


    setMaxMinDate(event)
    {
        this.minDate=new Date(this.selectedYear,this.selectedMonth)
      if(this.minDate<new Date())
      {
          this.minDate=new Date()
      }



        this.maxDate=new Date(this.selectedYear,this.selectedMonth+1,0)
        this.startAt=  this.minDate;

    }


    startDateChanged(type: string, event: MatDatepickerInputEvent<Date>)
    {
        if( this.minDate<event.value)
        {
            this.minDate=event.value
        }
    }


}
