import {MediaMatcher} from '@angular/cdk/layout';
import {ChangeDetectorRef, Component, HostBinding} from '@angular/core';
import {MessageBox} from './lib/Alet/message-box';
import {globalVariables} from './shared/globalVariables';
import {MatDialog} from '@angular/material';
import {RetrofitAngular} from './lib/RetrofitAngular';
import {UserInfoDto} from './dtos/userInfoDto';
import {AuthService} from './lib/auth/auth.service';
import {TranslateService} from '@ngx-translate/core';

import {LoginServices} from './services/login-services';
import {Router} from '@angular/router';
import {AddressBar} from './app-routing.module';
import {HomeComponent} from './pages/home/home.component';
import {HttpHeaders} from '@angular/common/http';
import {NgxSpinnerService} from './lib/spinner/ngx-spinner.service';
import { OverlayContainer} from '@angular/cdk/overlay';
import {MyEvents} from './lib/MyEvent';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  @HostBinding('class') componentCssClass;

constructor( private myEvents :MyEvents,public overlayContainer: OverlayContainer,private router:Router, private loginServices:LoginServices,private spinner: NgxSpinnerService, translate:TranslateService,  private auth: AuthService,  mtDialog:MatDialog)
{


  myEvents.subscribe('change:theme', (date) => {
    // this.overlayContainer.getContainerElement().classList.add(date["theme"]);
    // this.componentCssClass = date["theme"];

  });

MessageBox.dialog=mtDialog;
  translate.setDefaultLang('en');
  translate.use('en');
  globalVariables.translate=translate;
  globalVariables.loader=spinner;
  var x= translate.instant("GLOBAL_MESSAGES.OK")


}





  login() {

    const headers = new HttpHeaders().set("Content-Type", "application/json").set('Authorization', "Basic " +this.auth.getToken());

    RetrofitAngular.appHeaders = headers

      this.loginServices.login().subscribe((res: any) => {

          UserInfoDto.setUserInfoDto(res);
          this.router.navigate([AddressBar.HomePageComponent])
        },
        (err: any) => {

this.router.navigate([AddressBar.LoginPageComponent])
        }
      );


  }




}
