import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../lib/auth/auth.service';
import { MyEvents } from '../../lib/MyEvent';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSlideToggle} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';
import {IProvider} from '../../interfaces/IProvider';
import {AssignedProvidersServcies} from '../../services/assigned-providers-servcies.service';
import {AddressBar} from '../../app-routing.module';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {globalVariables, ServiceTypeId} from '../../shared/globalVariables';
import {NavParams} from '../../shared/nav-params';
import {m1} from '../../lib/model-hanafy/m1';
import {Router} from '@angular/router';

@Component({
  selector: 'app-providers',
  templateUrl: './providers.component.html',
  styleUrls: ['./providers.component.scss']
})
export class ProvidersComponent implements OnInit {
  columnNum;
  originalMyAssignedProviders:Array<IProvider>=[];
  tempAssignedProviders:Array<IProvider>=[];
  serviceTypeId:ServiceTypeId;
  providerTypeName;
  constructor(private router:Router, private assignedProvidersServcies:AssignedProvidersServcies, public media: ObservableMedia, public dialog: MatDialog, private myevents :MyEvents, private authService: AuthService) {

    if(  this.router.url=="/"+AddressBar.Pharmacies)
    {
      this.serviceTypeId=3
    }
    else if(  this.router.url=="/"+AddressBar.Doctors)
    {
      this.serviceTypeId=1
    }
    else if(  this.router.url=="/"+AddressBar.Hospitals)
    {
      this.serviceTypeId=2
    }


    this.providerTypeName= ServiceTypeId[ this.serviceTypeId];



this.assignedProvidersServcies.providers(this.serviceTypeId).subscribe((res: any) => {
    this.originalMyAssignedProviders=res["data"];
    this.onScroll();
  },
  (err: any) => {

    globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

  });
    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 3;
        }
      });
  }

  ngOnInit() {
  }


  onChange(event,provider:IProvider)
  {

    var temp=event;
    if(event.checked===true) {
      // (event as any).source.toggle()

      this.assignedProvidersServcies.addAssignedProvider(provider.id ).subscribe((res: any) => {
          provider.assignedId=res["data"]["assignedId"]
        },
        (err: any) => {
          temp.source.toggle()
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });
    }
    else
    {
      this.assignedProvidersServcies.removeAssignedProvider(provider.assignedId).subscribe((res: any) => {

        },
        (err: any) => {
          temp.source.toggle()
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        });

    }

  }



  makeOrder()
  {
    let dialogRef = this.dialog.open(m1, {
      width: '350px',

    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }



  onScroll()
  {
    var that=this;
    setTimeout(()=>{    //<<<---    using ()=> syntax
      var scrollItemCountonce=20;
      var tempCount;
      if(that.tempAssignedProviders.length < that.originalMyAssignedProviders.length){
        let len = this.tempAssignedProviders.length;

        if( that.originalMyAssignedProviders.length -that.tempAssignedProviders.length >scrollItemCountonce)
        {
          tempCount=scrollItemCountonce;
        }
        else
        {
          tempCount=(that.originalMyAssignedProviders.length -that.tempAssignedProviders.length);
        }
        for(var i = len; i < len+tempCount; i++){
          that.tempAssignedProviders.push(that.originalMyAssignedProviders[i]);
        }
      }
    }, 100);

  }


  onUp()
  {
    if(this.tempAssignedProviders.length < this.originalMyAssignedProviders.length){
      let len = this.tempAssignedProviders.length;

      for(var i = len; i <= len+20; i++){
        this.tempAssignedProviders.push(this.originalMyAssignedProviders[i]);
      }
    }
  }

}
