import {
  Component, Input, NgZone, ViewEncapsulation, ViewChild, OnInit, NgModule, OnDestroy
} from '@angular/core';
import {DocCategory, DocumentationItems} from './documentation-items';
import {MatSidenav, MatSidenavModule, MatIconModule, MatButton} from '@angular/material';
