import {NavParams} from '../nav-params';

const SMALL_WIDTH_BREAKPOINT = 720;

@Component({
  selector: 'app-component-sidenav',
  templateUrl: './component-sidenav.html',
  styleUrls: ['./component-sidenav.scss'],
  encapsulation: ViewEncapsulation.None,

  animations: [
    trigger('bodyExpansion1', [
      state('collapsed', style({height: '0px', display: 'none'})),
      state('expanded', style({height: '*', display: 'block'})),
      transition('* => void', [

        animate(10000, style({height: 0}))
      ]),
    ]),
    trigger('bodyExpansion2', [
      state('collapsed', style({height: '0px', display: 'none'})),
      state('expanded', style({height: '*', display: 'block'})),
      transition('expanded <=> collapsed',
        animate(2000, style({height: 0}))
      ),
    ]),
    trigger('bodyExpansion2', [
      state('collapsed', style({height: '0px', display: 'none',opacity:0 })),
      state('expanded', style({height: '*' , display: 'block',opacity:1})),
      transition('expanded <=> collapsed', [
        style({height: '*'}),
        animate(250, style({height: 0}))
      ])
    ]),
    trigger('bodyExpansion', [
      state('collapsed', style({height: '0px', display: 'none', opacity: 0})),
      state('expanded', style({height: '*', display: 'block', opacity:1})),
      transition('expanded <=> collapsed', animate('250ms cubic-bezier(1,1,1,1)')),
    ]),


    // trigger('slideInOut', [
    //   state('in', style({height: '*', opacity: 0})),
    //   transition(':leave', [
    //     style({height: '*', opacity: 1}),
    //
    //     group([
    //       animate(300, style({height: 0})),
    //       animate('200ms ease-in-out', style({'opacity': '0'}))
    //     ])
    //
    //   ]),
    //   transition(':enter', [
    //     style({height: '0', opacity: 0}),
    //
    //     group([
    //       animate(300, style({height: '*'})),
    //       animate('400ms ease-in-out', style({'opacity': '1'}))
    //     ])
    //
    //   ])
    // ])
  ]


  ,
})
export class ComponentSidenav implements OnInit {
  private mediaMatcher: MediaQueryList = matchMedia(`(max-width: ${SMALL_WIDTH_BREAKPOINT}px)`);

  params: Observable<Params>;
  watcher: Subscription;
  sidenavPosition = 'over';
  isSidenavOpen = true;
  isSidenavCloseDisabled = true;
  isMobile;

  claimsStatic=ClaimsStatic;

  serviceTypeId = ServiceTypeId;
  @ViewChild('sidenav') public sidenav: MatSidenav;
    @ViewChild('elToFocus') elToFocus:MatButton;


  expansions = {};
  xxx=[];
  private _onDestroy = new Subject<void>();



  constructor(public docItems: DocumentationItems,
              private  media: ObservableMedia,private myEvents :MyEvents,
              private _route: ActivatedRoute,
                private navParams:NavParams,
              private router: Router,

              zone: NgZone) {
    this.mediaMatcher.addListener(mql => zone.run(() => this.mediaMatcher = mql));


    this.watcher = media.subscribe((change: MediaChange) => {
      if (change.mqAlias === 'xs') {
        this.sidenavPosition = 'over';
        this.isSidenavCloseDisabled = false;
        this.isMobile=true;
        if(this.sidenav)
        {
          this.sidenav.close()

        }

      } else {
        this.isSidenavOpen = true;
        this.isSidenavCloseDisabled = true;
        this.sidenavPosition = 'side';
        if(this.sidenav)
        {
          this.sidenav.open()
        }
      }
    });

    myEvents.subscribe('menu:toggle', (test) => {
      this.sidenav.toggle()
    });


    // TODO(josephperrott): Move to CDK breakpoint management once available.
    this.mediaMatcher.addListener(mql => zone.run(() => this.mediaMatcher = mql));
  }



  ngOnInit() {

    this.xxx= this.docItems.getCategories()
    this.setExpansions(null)


    this.router.events.subscribe(() => {
      if (this.isScreenSmall()) {
      // this.sidenav.toggle()
          this.sidenav.close();

          var that=this;
          setTimeout(() => {
              that.elToFocus.focus()


          }, 500);

      }
      this.setExpansions(null)
    });



    // this._router.eventsUI.pipe(
    //   startWith(null),
    //   switchMap(() => {
    //     console.log("dddddd")
    //     return []
    //   }),
    //   takeUntil(this._onDestroy)
    // ).subscribe(p => {
    //   if (this.isScreenSmall()) {
    //     this.sidenav.close();
    //   }
    //   this.setExpansions(p)
    // });


    // this.xxx= this.docItems.getCategories((this.params.section))



    // Combine params from all of the path into a single object.
    this.params = combineLatest(
      this._route.pathFromRoot.map(route => {
        return route.params
      }),
      Object.assign);
  }

  ngOnDestroy () {
    this.watcher.unsubscribe();
    this._onDestroy.next();
    this._onDestroy.complete();
  }

  isScreenSmall(): boolean {
    return this.mediaMatcher.matches;
  }


  // closeif()
  // {
    // if(!this.isSidenavCloseDisabled )
    // {
    //   this.sidenav.toggle()
    // }
  // }

  /** Set the expansions based on the route url */
  setExpansions(params: Params) {
    const categories = this.docItems.getCategories();
    for (const category of (categories || [])) {
     //this.expansions[category.url]=true;
      if (this.expansions[category.url] === true) {
        continue;
      }

      let match = false;
      if(category.items)
      {
      for (const item of category.items) {
        if (this.router.url.indexOf(item.url) > -1) {
          match = true;
          break;
        }
      }

      }

      if (this.expansions[category.url] === false) {
        this.expansions[category.url] = match;
      }
    }
  }

  /** Gets the expanded state */
  _getExpandedState(category: DocCategory) {
    return this.getExpanded(category) ? 'expanded' : 'collapsed';
  }

  /** Toggles the expanded state */
  toggleExpand(category: DocCategory) {

    this.expansions[category.url] = !this.expansions[category.url];
    // if(category.items.length<=0)
    // {
    //
    //   this._router.navigate( [category.url ])
    // }
  }

  /** Gets whether expanded or not */
  getExpanded(category: DocCategory): boolean {
    return this.expansions[category.url] === undefined ? this.checkIfchiledIsSelected(category) : this.expansions[category.url];
  }

  checkIfchiledIsSelected(category):boolean
  {
    if(category.items)
    {
      for (const item of category.items) {
        if (this.router.url.indexOf(item.url) > -1) {
          return true;
        }
      }
      return false;
    }

    return false;
  }
  openItem(item)
  {

   // this.navParams.data={"test":"aaaaaaaaaaaaaa"}
    this.router.navigate([ item.url] );
   // this.closeif()

  }


  getClassCategory(category)
  {

      if (!category.items || category.items.length <= 0) {
        if((this.router.parseUrl(this.router.url).root.children as any).primary.segments[0].path== category.url)
        {
          return 'color-blue'
        }
        else
        {
          return 'color-not-black'
        }

      }
      else {
        var xx = category.items.filter(obj =>(this.router.parseUrl(this.router.url).root.children as any).primary.segments[0].path== obj.url)

        if(xx.length>0)
        {
          return 'color-black'
        }
        else
        {
          return 'color-not-black-category'
        }
        // for (var item of category.items) {
        //   console.log(item)
        //   if(this.router.url.indexOf(item.url)>-1)
        //   {
        //     return 'color-black'
        //   }
        //   else
        //   {
        //     return 'color-not-black'
        //   }
        // }




      }



  }


}




