import {Component, EventEmitter, Input, Output} from "@angular/core";

@Component({
    selector: 'custom-spinner',
    templateUrl: 'customSpinner.html',
  styleUrls: ['customSpinner.scss']

})
export class customSpinner {

    @Input('errorMessage') errorMessage = "";
    @Input('showSpinner') showSpinner = true;
    @Input('disableRetry') disableRetry = false;
    @Output() retry = new EventEmitter();

    constructor() {
        //this.retry.emit();
    }

    emitRery() {
        this.retry.emit();
    }

}
