import {Component, Inject, OnInit, ViewEncapsulation} from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NavParams } from '../../shared/nav-params';
import { AuthService } from '../../lib/auth/auth.service';
import {RetrofitAngular} from '../../lib/RetrofitAngular';
import {LoginServices} from '../../services/login-services';
import {globalVariables} from '../../shared/globalVariables';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {TranslateService} from '@ngx-translate/core';
import {HttpHeaders} from '@angular/common/http';
import {AddressBar} from '../../app-routing.module';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit {

  formGroup1;

  constructor(private translate: TranslateService,private loginServices:LoginServices,private fb: FormBuilder,private router: Router,private navParams:NavParams, private authService: AuthService) {



    this.formGroup1 = fb.group({
      email: ['', [Validators.required]], //, Validators.email
      password: ['', Validators.required]
    });




  }

  ngOnInit() {
  }

  login() {

    if (this.formGroup1.valid) {
      var temp = "Basic " + btoa(this.formGroup1.controls["email"].value + ":" + this.formGroup1.get("password").value);

      RetrofitAngular.appHeaders=  RetrofitAngular.appHeaders.set('Authorization', temp)



      this.loginServices.login().subscribe((res: any) => {

          UserInfoDto.setUserInfoDto(res["data"]);

          this.authService.saveUserDto(res["data"])
          this.authService.saveToken( btoa(this.formGroup1.controls["email"].value + ":" + this.formGroup1.get("password").value))


          this.navParams.data = {"test": "aaaaaaaaaaaaaa"}
          this.router.navigate(['']);
          // globalVariables.changeLang(globalVariables.translate.currentLang)

        },
        (err: any) => {
          console.log("FAILLLLLLLL1....",err);


          // globalVariables.alert(globalVariables.getErrorMessage(err),"Error")
          var that = this;
            globalVariables.alert(globalVariables.getErrorMessage(err), that.translate.instant("GLOBAL_MESSAGES.ERROR"))
            // globalVariables.alert("test")



        }
      );









    }
  }


  autoLogin() {


    this.loginServices.login().subscribe((res: any) => {

        UserInfoDto.setUserInfoDto(res);
        this.router.navigate([AddressBar.HomePageComponent])
      },
      (err: any) => {

       // this.router.navigate([AddressBar.LoginPageComponent])
      }
    );


  }



}
