import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {AddressBar} from '../../app-routing.module';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {DateTimeService} from '../DateTimeService';
import {RetrofitAngular} from '../RetrofitAngular';
import {HttpHeaders} from '@angular/common/http';

@Injectable()
export class AuthService {


  expireAfterMinutes=9999999999999999;
  public static  UserClaims:string|number|(string|number)[]=[]
  public static   UserRules:string|number|(string|number)[] ;

  constructor(private dateTimeService:DateTimeService ) { }

  saveToken(token: string) {
    localStorage.setItem("token", token)
    // if(!RetrofitAngular.appHeaders.has("Authorization"))
    // {
    //   RetrofitAngular.appHeaders=  RetrofitAngular.appHeaders.append('Authorization', "Basic " +token)
    // }
    this.saveExpire()
  }

  getToken() {
    if(  this.checkExpire()) {
     var token= localStorage.getItem("token")
      // if(!RetrofitAngular.appHeaders.has("Authorization"))
      // {
      //   RetrofitAngular.appHeaders=   RetrofitAngular.appHeaders.append('Authorization', "Basic " +token)
      // }
      return token
    }
    else {
      localStorage.removeItem("token")
      return null;
    }

  }

  saveUserDto(userDTO: any) {
    localStorage.setItem("userDTO",btoa(JSON.stringify(userDTO) ))
    this.saveExpire();

  }

  saveExpire()
  {

    const expredate = new Date().valueOf()+(this.expireAfterMinutes * 60000);
    //localStorage.setItem("expireUserInfoDateTime",this.dateTimeService.DateToStringDateAndTime(expredate))
    localStorage.setItem("expire", expredate.toString())
  }

  getUserDtoFromLocal() {


    if(  this.checkExpire())
    {
      var temp=localStorage.getItem("userDTO")
      if(temp)
      {
        if(!RetrofitAngular.appHeaders.has("Authorization"))
        {
          RetrofitAngular.appHeaders=   RetrofitAngular.appHeaders.append('Authorization', "Basic " +localStorage.getItem("token"))
        }
        return JSON.parse(atob( temp))
      }
    }
    else
    {
      localStorage.removeItem("userDTO")
      return null;
    }

  }

  checkExpire()
  {
    const expredate = new Date().valueOf()
    var localx= parseInt( localStorage.getItem("expire"))

    if(localx&& localx>expredate) {

        return true;

    }
    else {
      return false;
    }
  }

  isLoggednIn() {
    if(UserInfoDto.id!=null)
    {
        var that=this;
      setTimeout(function () {
        that.saveExpire();

      }, 1000)
      return true;

    }
    else
    {
      var info=this.getUserDtoFromLocal()
      if( info)
      {
        UserInfoDto.setUserInfoDto(info)
        return true;
      }
      else {
        return false;
      }
    }

  }

  logout() {
    localStorage.removeItem("token");
    localStorage.removeItem("userDTO");
    localStorage.removeItem("expire");
    UserInfoDto.clearUser();
  }


  checkIfUserHasRole(role_s)
  {
   return checkIfIn(role_s,AuthService.UserRules)
  }

  checkIfUserHasClaim(claim_s)
  {
   return checkIfIn(claim_s,AuthService.UserClaims)
  }

}


export   function Roles(roles:string|number|(string|number)[] ) {//these parameters which are sent from the service like @Post("/login/checkPassword", new Headers) defined above Post
  return function (targetClass: any, methodName: string, descriptor: any) {
    //   var pPath = targetClass[`${methodName}_Path_parameters`];
    let method = descriptor.value;
    descriptor.value = function (...args: any[]) {

      // console.log("role:",roles)
      if(checkIfIn(roles,AuthService.UserRules))
      {
        return method.apply(this, arguments);
      }
      else
      {
        throw new Error("Error:Missing permissions")
      }


    };

    return descriptor;
  }





}




export function checkIfIn(roles,auth_roules)
{
  if( Array.isArray(roles))
  {
    for (var i = 0; i < roles.length; i++) {
      if(typeof auth_roules === 'number')
      {
        if( auth_roules==roles[i])
        {

          return true
        }
      }
      else if((auth_roules as any).indexOf(roles[i]) > -1)
      {
        return true
      }

    }
    return false;
  }
  else
  {
    if(typeof auth_roules=== 'number')
    {
      if( auth_roules==roles)
      {
        return true;
      }
    }
    else if((auth_roules).indexOf(roles)>-1)
    {
      return true;
    }
    return false
  }
}




export   function Claims(claims:string|number|(string|number)[] ) {//these parameters which are sent from the service like @Post("/login/checkPassword", new Headers) defined above Post
  return function (targetClass: any, methodName: string, descriptor: any) {
    //   var pPath = targetClass[`${methodName}_Path_parameters`];
    let method = descriptor.value;
    descriptor.value = function (...args: any[]) {
      if(checkIfIn(claims,AuthService.UserClaims))
      {
        return method.apply(this, arguments);
      }
      else
      {
        throw new Error("Error:Missing permissions")
      }
    };

    return descriptor;
  }
}


