import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {SelectionModel} from "@angular/cdk/collections";
import {Subject} from "rxjs";
import {MyTableDataSource} from "../../../lib/myInlineEditTable/MyTableDataSource";
import {MatDialog, MatPaginator, MatSort} from "@angular/material";
import {NavParams} from "../../../shared/nav-params";
import {ActivatedRoute, NavigationEnd, Router} from "@angular/router";
import {DateTimeService} from "../../../lib/DateTimeService";
import {CoachingServices} from "../../../services/CoachingServices";
import {MediaChange, ObservableMedia} from "@angular/flex-layout";
import {UserInfoDto} from "../../../dtos/userInfoDto";
import {globalVariables} from "../../../shared/globalVariables";
import {MyDataStructure} from "../../reports/coaching/coaching-summary.component";
import {PlanServices} from "../../../services/PlanServices";
import {UserData} from "../../product/product.component";
import {OffModalComponent} from "../../off-visit/off-modal/off-modal.component";
import {CreateNewModal} from "../create-new-modal/create-new-modal";
import {AddressBar} from "../../../app-routing.module";
import {debounceTime, filter} from "rxjs/operators";
import {MyEvents} from "../../../lib/MyEvent";

@Component({
  selector: 'app-plan-list',
  templateUrl: './plan-list.component.html',
  styleUrls: ['./plan-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class PlanListComponent implements OnInit {
    columnNum;
    expanded;
    selectedDatePickerFrom;
    selectedDatePickerTo;


    mydata=new Subject<any[] >();



  displayedColumns = [     "planId","employeeName"
        ,"startDate"
        ,"endDate"
        ,"subject"
        ,"status"
        ,"comment"  ];

    dataSource: MyTableDataSource<MyDataStructure>;
    planList=[];
    type;

    private paginator: MatPaginator;
    private sort: MatSort;
    @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
        this.paginator = mp;
        if(this.dataSource) {
            this.dataSource.paginator = this.paginator;
        }
    }

    @ViewChild(MatSort) set matSort(mp: MatSort) {
        this.sort = mp;
        if(this.dataSource)
        {
            this.dataSource.sort = this.sort;
        }
    }

    constructor(  private myEvents :MyEvents, private navParams:NavParams,public route: ActivatedRoute, private router: Router, private dateTimeService:DateTimeService,private PlanServices:PlanServices,  public media: ObservableMedia, public dialog: MatDialog ) {


        if(  this.router.url=="/"+AddressBar.PlanReviewApproval)
        {
            this.type=AddressBar.PlanReviewApproval;

        }
        else
        {
            this.type=AddressBar.MyPlan;

          this.displayedColumns  =[     "planId"
                ,"startDate"
                ,"endDate"
                ,"subject"
                ,"status"
                ,"comment"  ];
        }


        myEvents.subscribe('refreshPlanlist', (planId) => {
            this.planList.find(item=>item.planId==planId).status="SENT_FOR_APPROVAL"
        });

        myEvents.subscribe('createPlanlist', (listItem) => {
            this.planList.push(listItem);
        });

        myEvents.subscribe('approve-reject-Plan', (planId) => {
            this.planList=   this.planList.filter(item=>item.planId!=planId)

            this.dataSource = new MyTableDataSource(this.planList, UserData, null);

        });




        var date = new Date();
        this.selectedDatePickerFrom = new Date(date.getFullYear(), date.getMonth(), 1);
        this.selectedDatePickerTo = date;

        this.getPlanList();


        media.asObservable()
            .subscribe((change: MediaChange) => {
                // alert(change.mqAlias);
                console.log(change.mqAlias);
                if(change.mqAlias == 'xs'){
                    this.columnNum = 1;
                }
                else if(change.mqAlias == 'sm'){
                    this.columnNum = 1;
                }
                else if(change.mqAlias == 'md'){
                    this.columnNum = 2;
                }
                else{
                    this.columnNum = 3;
                }
            });
    }



  ngOnInit() {
console.log("1111111")
  }
    ngAfterViewInit() {
        console.log("22222222")
        // let draggable:Draggable =
        //     new Draggable(this.element.nativeElement,{clone: false});
    }

    getPlanList()
    {

        if(this.type==AddressBar.PlanReviewApproval)
        {
            this.PlanServices.waitingForApprovalPlansList().subscribe((res: any) => {
                    this.planList = res["data"];
                    this.dataSource = new MyTableDataSource(this.planList, UserData, null);
                    this.dataSource.paginator = this.paginator;
                    this.dataSource.sort = this.sort;
                },
                (err: any) => {
                    globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

                }
            );
        }
        else
        {
            this.PlanServices.planList().subscribe((res: any) => {
                    this.planList = res["data"];
                    this.dataSource = new MyTableDataSource(this.planList, UserData, null);
                    this.dataSource.paginator = this.paginator;
                    this.dataSource.sort = this.sort;
                },
                (err: any) => {
                    globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

                }
            );
        }

    }




    goTo(row)
    {

        this.navParams.data=row;
        if(this.type==AddressBar.PlanReviewApproval)
        {

            this.router.navigate([AddressBar.PlanReviewApproval+'/'+ AddressBar.PlanDetails] );
        }
        else
        {

            this.router.navigate([AddressBar.MyPlan+'/'+ AddressBar.PlanScheduler] );
        }

    }


    newPlan()
    {
        let dialogRef = this.dialog.open(CreateNewModal, {
            // width: '250px',
            data: {}
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log(result);

        });
    }


    applyFilter(filterValue: string) {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }
}
