import { Component, OnInit } from '@angular/core';
import { AddressBar } from '../../app-routing.module';
import { ObservableMedia, MediaChange } from '@angular/flex-layout';
import { MatDialog } from '@angular/material';
import { MyEvents } from '../../lib/MyEvent';
import { AuthService } from '../../lib/auth/auth.service';
import { NavParams } from '../../shared/nav-params';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import {globalVariables} from '../../shared/globalVariables';
import {ApproveHTTPServices} from '../../services/approve_http_services.service';
 import {IRequest} from '../../interfaces/IRequest';
import {DateTimeService} from '../../lib/DateTimeService';


@Component({
  selector: 'app-approve-schedule-details',
  templateUrl: './approve.component.html',
  styleUrls: ['./approve.scss']
})
export class ApproveComponent implements OnInit {

  columnNum;
 PendingRequests:Array<IRequest>=[];

  constructor(public dateTimeService:DateTimeService, public approveHTTPServices:ApproveHTTPServices,private _location: Location,private navParams:NavParams,private router: Router, public media: ObservableMedia, public dialog: MatDialog, private myevents :MyEvents,private authService: AuthService) {



    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 2;
        }
      });
  }

  ngOnInit() {
this.getList();
  }

  goBack() {
    this._location.back();
  }


  getList()
  {
    this.approveHTTPServices.pendingApproval().subscribe((res: any) => {
        this.PendingRequests=res["data"];
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });
  }

  public approve(dubleVisit,item)
  {


    this.approveHTTPServices.approveRequest(item.requestId,item.requestTypeId,dubleVisit).subscribe((res: any) => {


          this.removeCard(item);

        globalVariables.alert("Approved Successfully")

      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });
  }


  public reject(item)
  {

    this.approveHTTPServices.rejectRequest(item.requestId,item.requestTypeId).subscribe((res: any) => {

          this.removeCard(item);


        globalVariables.alert("Rejected Successfully")
      },
      (err: any) => {

        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      });
  }



  removeCard(item)
  {
    this.PendingRequests=this.PendingRequests.filter(obj =>
    {
      if(obj==item)
      {
       return false;
      }
      else
      {
        return true;
      }
      // if( obj.requestId!=item.requestId && obj.requestTypeId!=item.requestTypeId)
      // {
      //   return true;
      // }
      // else
      // {
      //   return false
      // }
    })
  }



  filterRequests(requestTypeId)
  {
    return this.PendingRequests.filter( obj=>obj.requestTypeId ==requestTypeId);
  }



}
