import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSlideToggle, MatPaginator, MatSort} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';

import {ActivatedRoute, Router} from '@angular/router';
import {CoachingServices} from '../../../services/CoachingServices';
import {globalVariables} from '../../../shared/globalVariables';
import {UserInfoDto} from '../../../dtos/userInfoDto';
import {SelectionModel} from '@angular/cdk/collections';
import {Subject} from 'rxjs';
import {MyTableDataSource} from '../../../lib/myInlineEditTable/MyTableDataSource';
import {DateTimeService} from '../../../lib/DateTimeService';
import {AddressBar} from '../../../app-routing.module';
import {NavParams} from '../../../shared/nav-params';
import {GroupByServices} from '../../../services/GroupByServices';
import {VisitInfoComponent} from './visit-detail/visitInfo/visitInfo.component';
import {LocationStrategy} from '@angular/common';


export class MyDataStructure {
  id: string;
  name: string;
  count:number;

}
@Component({
  selector: 'group-by',
  templateUrl: './groupBy.component.html',
  styleUrls: ['./groupBy.component.scss']
})
export class GroupByComponent implements OnInit {
  columnNum;
  expanded;
  selectedDatePickerFrom;
  selectedDatePickerTo;
  emp=[];
  checkList=new SelectionModel<any>(true /* multiple */);
  //mydata =new Subject<TodoItemNode[] >();
  mydata=new Subject<any[] >();

  displayedColumns = [  'name', 'count' ];
  dataSource: MyTableDataSource<MyDataStructure>;
  tableData=[];
  type;
title;

   private paginator: MatPaginator;
  private sort: MatSort;
  @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
    this.paginator = mp;
    if(this.dataSource) {
      this.dataSource.paginator = this.paginator;
    }
  }

  @ViewChild(MatSort) set matSort(mp: MatSort) {
    this.sort = mp;
    if(this.dataSource)
    {
      this.dataSource.sort = this.sort;
    }
  }




  constructor( location: LocationStrategy,private navParams:NavParams,public route: ActivatedRoute, private router: Router, private dateTimeService:DateTimeService,private groupByservices:GroupByServices,  public media: ObservableMedia, public dialog: MatDialog ) {


    location.onPopState((vv) => {
      console.log("vv",vv)
if(vv.type=="hashchange")
{

  setTimeout(()=>{

    // this.dataSource.paginator = this.paginator;
    //  this.dataSource.sort = this.sort;

  }, 1000)

}
      //  this.dataSource.paginator = this.paginator;
      // this.dataSource.sort = this.sort;

      // this.dataSource._renderChangesSubscription()renderRows();        // call from typescript

    });

    var date = new Date();
    this.selectedDatePickerFrom = new Date(date.getFullYear(), date.getMonth(), 1);
    this.selectedDatePickerTo = date;
    if(route.snapshot['_routerState'].url=="/"+AddressBar.GroupByEmployee)
    {
      this.type="employee"
        this.title="Visits Grouped By Employee"
    }
    else if(route.snapshot['_routerState'].url=="/"+AddressBar.GroupByProduct)
    {
        this.type="product"
        this.title="Visits Grouped By Product"
    }
    else if(route.snapshot['_routerState'].url=="/"+AddressBar.GroupByFLM)
    {
        this.type="FLM";
        this.title="Visits Grouped By FLM"
    }
    else if(route.snapshot['_routerState'].url=="/"+AddressBar.GroupByDistrict)
    {
        this.type="district";
        this.title="Visits Grouped By district"
    }

    else if(route.snapshot['_routerState'].url=="/"+AddressBar.GroupBySpecialty)
    {
        this.type="specialty";
        this.title="Visits Grouped By Specialty"
    }



    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 3;
        }
      });
  }




  search()
  {


    if(!this.selectedDatePickerFrom)
    {
      globalVariables.alert("Please select From Data.")
      return;
    }
    if(!this.selectedDatePickerTo)
    {
      globalVariables.alert("Please select To Data.")
      return;
    }



    this.expanded=false;
    setTimeout(()=>{

      if(this.type=="employee") {
        this.groupByservices.visitsPerEmployee(this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom), this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)).subscribe((res: any) => {
            this.expanded = false;
            this.tableData = res["data"];
            this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          },
          (err: any) => {
            this.expanded = false;
            globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

          }
        );
      }
      else if(this.type=="product")
      {
          this.groupByservices.visitsPerProduct(this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom), this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)).subscribe((res: any) => {
                  this.expanded = false;
                  this.tableData = res["data"];
                  this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                  this.dataSource.paginator = this.paginator;
                  this.dataSource.sort = this.sort;
              },
              (err: any) => {
                  this.expanded = false;
                  globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

              }
          );
      }

      else if(this.type=="FLM")
      {
          this.groupByservices.visitsPerFLM(this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom), this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)).subscribe((res: any) => {
                  this.expanded = false;
                  this.tableData = res["data"];
                  this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                  this.dataSource.paginator = this.paginator;
                  this.dataSource.sort = this.sort;
              },
              (err: any) => {
                  this.expanded = false;
                  globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

              }
          );
      }

      else if(this.type=="district")
      {
          this.groupByservices.visitsPerDistrict(this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom), this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)).subscribe((res: any) => {
                  this.expanded = false;
                  this.tableData = res["data"];
                  this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                  this.dataSource.paginator = this.paginator;
                  this.dataSource.sort = this.sort;
              },
              (err: any) => {
                  this.expanded = false;
                  globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

              }
          );
      }

      else if(this.type=="specialty")
      {
          this.groupByservices.visitsPerSpecialty(this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom), this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)).subscribe((res: any) => {
                  this.expanded = false;
                  this.tableData = res["data"];
                  this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
                  this.dataSource.paginator = this.paginator;
                  this.dataSource.sort = this.sort;
              },
              (err: any) => {
                  this.expanded = false;
                  globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

              }
          );
      }

    }, 100)






  }




  ngOnInit() {
  }




  alert()
  {

    console.log(this.checkList)
  }

  getTotal()
  {
    if( this.tableData.length>0)
    {
      return this.tableData.map(t => t.count).reduce((acc, value) => acc + value, 0);

    }
    else
    {
      return "";
    }

    //
  }



  goTo(row)
  {
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] ,{ queryParams: { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)} }  );
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}]    );
    // this.router.navigate([AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee,{ "employeeId": row.employeeId,"employeeNamewwwww":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}] , { skipLocationChange: true}  );

    if(row.count<=0)
    {
      globalVariables.alert("No visits.")
      return;
    }
    else {
      // this.router.navigateByData({
      //    url: [AddressBar.CoachingSummary+'/'+ AddressBar.CoachingEmployee] ,
      //    data:  { "employeeId": row.employeeId,"employeeName":row.employeeName,"dateFrom":this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerFrom),"dateTo": this.dateTimeService.DateToStringDateOnly( this.selectedDatePickerTo)}, //data - <any> type
      //    //extras: {} - <NavigationExtras> type, optional parameter
      //  });
      if(this.type=="employee") {
        this.navParams.data = {
          type: this.type,
          "employeeId": row.id,
          "employeeName": row.name,
          "dateFrom": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom),
          "dateTo": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)
        }
        this.router.navigate([AddressBar.GroupByEmployee + '/' + AddressBar.VisitDetails]);
      }

      else if(this.type=="product")
      {
          this.navParams.data = {
              type: this.type,
              "productId": row.id,
              "productName": row.name,
              "dateFrom": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom),
              "dateTo": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)
          }
          this.router.navigate([AddressBar.GroupByProduct + '/' + AddressBar.VisitDetails]);

      }

      else if(this.type=="FLM")
      {
          this.navParams.data = {
              type: this.type,
              "FLMId": row.id,
              "FLMName": row.name,
              "dateFrom": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom),
              "dateTo": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)
          }
          this.router.navigate([AddressBar.GroupByFLM + '/' + AddressBar.VisitDetails]);

      }

      else if(this.type=="district")
      {
          this.navParams.data = {
              type: this.type,
              "districtId": row.id,
              "districtName": row.name,
              "dateFrom": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom),
              "dateTo": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)
          }
          this.router.navigate([AddressBar.GroupByDistrict + '/' + AddressBar.VisitDetails]);

      }
      else if(this.type=="specialty")
      {
          this.navParams.data = {
              type: this.type,
              "specialtyId": row.id,
              "specialtyName": row.name,
              "dateFrom": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerFrom),
              "dateTo": this.dateTimeService.DateToStringDateOnly(this.selectedDatePickerTo)
          }
          this.router.navigate([AddressBar.GroupByDistrict + '/' + AddressBar.VisitDetails]);

      }






    }

  }




  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


}


