import { Injectable } from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, PRIMARY_OUTLET, UrlTree} from '@angular/router';
 import {AuthService, checkIfIn} from './auth.service';
import {Router} from '@angular/router';
import {AddressBar} from '../../app-routing.module';
import {globalVariables} from '../../shared/globalVariables';
import {Observable} from 'rxjs';
import {UserInfoDto} from '../../dtos/userInfoDto';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(private auth: AuthService,
    private myRoute: Router){

  }

  canActivate( next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {

if(this.myRoute.parseUrl( state.url ).root.children.primary)
{
  if ( this.isPageRefresh() && this.myRoute.parseUrl( state.url ).root.children.primary.segments.length>1) {
    console.warn( "Secondary view not allowed on refresh." );
    this.myRoute.navigateByUrl( this.getUrlWithoutSecondary( state ) );
    return( false );
  }
}




    const requiredRoles = next.data["requiredRoles"];
    const requiredClaims = next.data["requiredClaims"];


    // const expectedRole = route.data.expectedRole;
    //
    // const token = localStorage.getItem('token');
    //
    // // decode the token to get its payload
    // const tokenPayload = decode(token);
    //
    // if (!this.auth.isAuthenticated() || tokenPayload.role !== expectedRole) {
    //   this.router.navigate(['login']);
    //   return false;
    // }
    // return true;

    if(this.auth.isLoggednIn()){

      if(!requiredRoles && !requiredClaims)
      {
        return true;
      }

      if(requiredRoles)
      {
       if(! this.auth.checkIfUserHasRole(requiredRoles ))
       {
         globalVariables.alert("You do not have authority to view this page ")
         return false;
       }
      }
      if(requiredClaims)
      {
        if(!this.auth.checkIfUserHasClaim(requiredClaims) )
        {
          globalVariables.alert("You do not have authority to view this page ")
          return false;
        }
      }

      return true;


    }
    else
    {
      this.myRoute.navigate([AddressBar.LoginPageComponent]).then(dd=>{
        if(this.auth.getToken() && state.url== "/")
        {

          globalVariables.showProgressDialog()
        }
      });
    }






  }





  private isPageRefresh() : boolean {
// If the router has yet to establish a single navigation, it means that this
// navigation is the first attempt to reconcile the application state with the
// URL state. Meaning, this is a page refresh.

//        console.log(this.myRoute.navigated)
// console.log("navigation.type",performance.navigation.type)
//     if(performance.navigation.type==0)
//     {
//       return false
//     }
//     else
//     {
//       return true;
//     }

    return( ! this.myRoute.navigated );
  }

  private getUrlWithoutSecondary( routerStateSnapshot: RouterStateSnapshot ) : UrlTree {
    var urlTree = this.myRoute.parseUrl( routerStateSnapshot.url );
    var segment = urlTree.root;
// Since the "secondary" outlet is known to be directly off the primary view
// (ie, not nested within another named-outlet), we're going to walk down the
// tree of primary outlets and delete any "secondary" children. This should
// leave us with a UrlTree that contains everything that the original URL had,
// less the "secondary" named-outlet.

  //  segment=  segment.children.primary.segments[0]

   //delete segment.children.primary.segments[1]
    for (var i = 1; i < segment.children.primary.segments.length; i++) {
      if(i=1)
      {
        segment.children.primary.segments.splice(i, 1);
        i--;
      }

     }

   //  while ( segment && segment.children ) {
   //    delete( segment.children.secondary );
   //    segment = segment.children[ PRIMARY_OUTLET ];
   //  }
    return( urlTree );
  }


}
