import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSlideToggle, MatPaginator, MatSort} from '@angular/material';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';

import {ActivatedRoute, Router} from '@angular/router';
 import {globalVariables} from '../../shared/globalVariables';
import {UserInfoDto} from '../../dtos/userInfoDto';
import {SelectionModel} from '@angular/cdk/collections';
 import {Subject} from 'rxjs';
 import {MyTableDataSource} from '../../lib/myInlineEditTable/MyTableDataSource';
import {DateTimeService} from '../../lib/DateTimeService';
import {Location} from '@angular/common';
import {NavParams} from '../../shared/nav-params';
import {TargetVisitsServices} from '../../services/TargetVisitsServices';
import {AddressBar} from '../../app-routing.module';


export class MyDataStructure {
  id: string;
  name: string;
count:number;

}
@Component({
  selector: 'coaching-details',
  templateUrl: './target-visits.component.html',
  styleUrls: ['./target-visits.component.scss']
})
export class TargetVisitsComponent implements OnInit{
  columnNum;
  columnNum1;
  checkList=new SelectionModel<any>(true /* multiple */);
  //mydata =new Subject<TodoItemNode[] >();
  mydata=new Subject<any[] >();

  expanded;


  displayedColumns = ['firstName','lastName', 'specialty', 'district','targetVisits'];
  dataSource: MyTableDataSource<MyDataStructure>;

  tableData=[];
   employees=[];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  selectedRadio=null;
  currentPage;
  constructor(private navParams:NavParams, private _location: Location,private route: ActivatedRoute, private dateTimeService:DateTimeService,private targetVisitsServices:TargetVisitsServices, public media: ObservableMedia, public dialog: MatDialog ) {


    if(window.location.href.indexOf(AddressBar.TargetVisits)>=0)
    {
      this.currentPage="Target Visits";
    }
    else
    {
      this.currentPage=" Remaining Visits";
    }
    this.targetVisitsServices.lookupSubordinatesLowLevel().subscribe((res: any) => {
        this.employees=res["data"]
      if(this.employees.length<=0)
      {
        this.search(UserInfoDto.employeeId)
      }
        // var root={name:UserInfoDto.fullName,id:UserInfoDto.employeeId}
        // root["children"]=this.listToTree( res["data"]);
        // this.emp.push(root);
        // this.mydata.next(this.emp )
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );




    media.asObservable()
      .subscribe((change: MediaChange) => {
        // alert(change.mqAlias);
        console.log(change.mqAlias);
        if(change.mqAlias == 'xs'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'sm'){
          this.columnNum = 1;
        }
        else if(change.mqAlias == 'md'){
          this.columnNum = 2;
        }
        else{
          this.columnNum = 3;
        }
      });
  }






  ngOnInit() {

  }



  search(employeeId)
  {

    if(!employeeId&&!this.selectedRadio)
    {
      globalVariables.alert("Please select employee.")
      return;
    }
    if(!employeeId)
    {
      employeeId=this.selectedRadio.id
    }

    this.expanded=false;

    if(window.location.href.indexOf(AddressBar.TargetVisits)>=0)
    {
      this.targetVisitsServices.targetVisits(employeeId).subscribe((res: any) => {
          this.tableData = res["data"];
          this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        }
      );
    }

    else
    {
      this.targetVisitsServices.unvisit(employeeId).subscribe((res: any) => {
          this.tableData = res["data"];
          this.dataSource = new MyTableDataSource(this.tableData, MyDataStructure, null);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        }
      );
    }





  }



  listToTree(list) {

    var map = {}, node, roots = [], i;
    for (i = 0; i < list.length; i += 1) {
      map[list[i].id] = i; // initialize the map
      list[i].children = []; // initialize the children
    }
    for (i = 0; i < list.length; i += 1) {
      node = list[i];
      if (node.managerId != UserInfoDto.employeeId) {
        // if you have dangling branches check that map[node.parentId] exists
        if(!list[map[node.managerId]])
        {
          console.log("kkkkkk")
        }
        try {
          list[map[node.managerId]].children.push(node);

        }
        catch (e) {
console.error(node,e)
        }
      } else {
        roots.push(node);
      }
    }
    return roots;
  }




}


