import {Component, Inject, ViewChildren} from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA, MatDialogRef, MatDialogModule } from "@angular/material/dialog";


@Component({
  selector: "app-simple-dialog",
  templateUrl: "./simple-dialog.component.html",
  styleUrls: ["./simple-dialog.component.scss"]
})
export class SimpleDialogComponent {

  style: number;
  title: string;
  message: string;
  information: string;
  button: number;
  showInput=false;
  inputText
  allow_outside_click: boolean;
  constructor( public dialogRef: MatDialogRef<SimpleDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: any ) {
    console.log(data);
    this.style = data.style || 0;
    this.title = data.title;
    this.showInput=data.showInput;
    this.message = data.message;
    this.information = data.information;
    this.button = data.button;
    this.inputText=data.inputText;
    this.dialogRef.disableClose = !data.allow_outside_click || false;

  }



  onOk() {
    this.dialogRef.close(   {"button":"ok","inputText":this.inputText});
  }
  onCancel() {
    this.dialogRef.close(    {"button":"cancel","inputText":this.inputText});
  }
  onYes() {
    this.dialogRef.close( {"button":"yes","inputText":this.inputText} );
  }
  onNo() {
    this.dialogRef.close(  {"button":"no","inputText":this.inputText});
  }
  onAccept() {
    this.dialogRef.close(  {"button":"accept","inputText":this.inputText});
  }
  onReject() {
    this.dialogRef.close( {"button":"reject","inputText":this.inputText});
  }


}
