export class UserInfoDto {


  public static id: number;
  public static email: string;

  public static userRoleId:string;
  public static firstName: string;
  public static  lastName: string;
  public static  address: string;
  public static  phone1: string;
  public static  claims: Array<any>
  public static employeeId: number;
  public static employeeJobTitle: string;
  public static employeeNumber: string
  public static fullName:string;


  public static phone2: string;
  public static phone3: string;string;
  public static phone4: string;
  public static phone5: string;
  public static phone6: string;
  public static managerId: any;
  public static districtId: string;
  public static  governorateId:string;
  public static accountId:string;







    public static setUserInfoDto(customerInformationDto) {
    UserInfoDto.id= customerInformationDto.id;
    UserInfoDto.email= customerInformationDto.username;

    UserInfoDto.accountId= customerInformationDto.accountId;
    UserInfoDto.userRoleId= customerInformationDto.userRoleId;
    UserInfoDto.firstName= customerInformationDto.firstName;
    UserInfoDto.lastName= customerInformationDto.lastName;
    UserInfoDto.address= customerInformationDto.address;
    UserInfoDto.districtId= customerInformationDto.districtId;
    UserInfoDto.governorateId= customerInformationDto.governorateId;
    UserInfoDto.phone1= customerInformationDto.phone1;
    UserInfoDto.phone2= customerInformationDto.phone2;
    UserInfoDto.phone3= customerInformationDto.phone3;
    UserInfoDto.phone4= customerInformationDto.phone4;
    UserInfoDto.phone5= customerInformationDto.phone5;
    UserInfoDto.phone6= customerInformationDto.phone6;
    UserInfoDto.claims= customerInformationDto.claims;

    UserInfoDto.employeeId= customerInformationDto.employeeId;
    UserInfoDto.employeeJobTitle= customerInformationDto.employeeJobTitle;
    UserInfoDto.employeeNumber= customerInformationDto.employeeNumber;
    UserInfoDto.managerId= customerInformationDto.managerId;
      UserInfoDto.fullName=UserInfoDto.firstName + " "+UserInfoDto.lastName;



      AuthService.UserRules=UserInfoDto.userRoleId;
      AuthService.UserClaims=[];
      for (var i = 0; i < UserInfoDto.claims.length; i++) {
        AuthService.UserClaims.push(UserInfoDto.claims[i].id)
      }


    }


    public static clearUser()
    {
     UserInfoDto.id= null;
     UserInfoDto.email= null;
     UserInfoDto.accountId=null;
     UserInfoDto.userRoleId=null;
     UserInfoDto.firstName= null;
     UserInfoDto. lastName= null;
     UserInfoDto. address= null;
     UserInfoDto.districtId= null;
     UserInfoDto. governorateId=null;
     UserInfoDto. phone1= null;
     UserInfoDto.phone2= null;
     UserInfoDto.phone3= null;null;
     UserInfoDto.phone4= null;
     UserInfoDto.phone5= null;
     UserInfoDto.phone6= null;
     UserInfoDto. claims= [];
     UserInfoDto.employeeId= null;
     UserInfoDto.employeeJobTitle= null;
     UserInfoDto.employeeNumber= null
     UserInfoDto.managerId= null;
     UserInfoDto.fullName=null;
    }



}
