import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {FormBuilder, FormControl, Validators} from '@angular/forms';
import {globalVariables} from '../../../shared/globalVariables';
import {VisitesServices} from '../../../services/VisitesServices';
import {AssignedProvidersServcies} from '../../../services/assigned-providers-servcies.service';
import {ProductsServices} from '../../../services/ProductsServices';
import {DateTimeService} from '../../../lib/DateTimeService';
import {map, startWith} from 'rxjs/internal/operators';
import {Observable} from 'rxjs';

@Component({
  selector: 'complete-modal',
  templateUrl: './complete-modal.component.html',
  styleUrls: ['./complete-modal.component.scss']
})
export class CompleteModalComponent implements OnInit {

  formGroup1;
  singleOrDoubleVisit=0;
  selectedManagers=[];
  selectedProducts=[];
  products;
  comment;
  selectedDate;
  minDate=new Date();
  providers;
  filterdProviders :Observable<any[]>;
  selectedProvider;
  myControl = new FormControl();
  constructor( public dateTimeService:DateTimeService,private productsServices:ProductsServices,private assignedProvidersServcies:AssignedProvidersServcies,private visitesServices:VisitesServices,private fb: FormBuilder, public dialogRef: MatDialogRef<CompleteModalComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
    this.formGroup1 = fb.group({
      dropSelect: [this.data.selectedReason, [Validators.required]], //, Validators.email
      date: ['', Validators.required]
    });


    this.productBySpecialty(this.data.visit.specialtyId)


  }


  productBySpecialty(specialtyId)
  {
    this.productsServices.productBySpecialty(specialtyId).subscribe((res: any) => {
        this.products=res["data"]
      },
      (err: any) => {
        globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

      }
    );
  }


  ngOnInit() {



  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  onNgModelChange(event){
    console.log('on ng model change', event);
  }

  complete()
  {



    if(this.selectedProducts.length==0)
    {
      globalVariables.alert("Please select one Product at least.")
      return;
    }



    if( this.singleOrDoubleVisit==1 &&this.selectedManagers.length==0)
    {
      globalVariables.alert("Please select one manager at least.")
      return;
    }


    if(!this.comment)
    {
      globalVariables.alert("Please inter a comment.")
      return;
    }

    var selectedProductsIds = this.selectedProducts.map(product => product.id);
    var selectedManagersIds = this.selectedManagers.map(manager => manager.id);





      this.visitesServices.completeVisits( this.data.visit.visitId,selectedProductsIds,this.singleOrDoubleVisit,selectedManagersIds,this.comment).subscribe((res: any) => {
          this.dialogRef.close(this.data);
          globalVariables.alert("Visit completed successfully ")
        },
        (err: any) => {
          globalVariables.alert(globalVariables.getErrorMessage(err), globalVariables.translate.instant("GLOBAL_MESSAGES.ERROR"))

        }
      );

  }



  close()
  {
    this.dialogRef.close(null)
  }


  latestChangeEvent(event)
  {

    this.productBySpecialty(event.option.value.specialtyId)
    this.selectedProvider=event.option.value;

  }


  updateDateToString(event)
  {
    let newDate = new Date(event)
    this.selectedDate=newDate
  }


  displayFn(item): string | undefined {

    return item ? item.serviceProviderName.toString(): undefined;
  }

  filter(serviceProviderName: string): any[] {
    if(!serviceProviderName)
    {
      return this.providers
    }
    var xx= this.providers.filter(option =>
    {
      if(option.serviceProviderName) {
        return option.serviceProviderName.toLowerCase().indexOf(serviceProviderName.toLowerCase()) === 0
      }
      else
      {
        return false;
      }
    });

    return xx;
  }

}

